export const Storage = {
	set: <T>(key: string, value: T): void => {
		localStorage.setItem(key, JSON.stringify(value));
	},
	get: <T>(key: string, defaultValue: T | null = null): T => {
		const value = localStorage.getItem(key);
		if (!value) return defaultValue as T;
		if (!isJsonString(value)) return convertToJson<T>(key, value as T);
		return JSON.parse(value);
	},
	remove: (key: string): void => {
		localStorage.removeItem(key);
	},
	clear: (): void => {
		localStorage.clear();
	},
	check: (key: string): boolean => {
		return Boolean(localStorage.getItem(key));
	},
	length: (): number => {
		return localStorage.length;
	},
	compare: <T>(key: string, compareValue: T): boolean => {
		const value = localStorage.getItem(key);
		return value === JSON.stringify(compareValue);
	},
};

export function isJsonString<T>(str: T): boolean {
	try {
		JSON.parse(str as string);
	} catch (e) {
		return false;
	}
	return true;
}

function convertToJson<T>(key: string, value: T): T {
	Storage.set(key, value);
	return value;
}

window.addEventListener("load", () => {
	if (!storageAvailable("localStorage")) {
		createSnackBar({
			message: "Bitte Cookies zulassen!",
			dismissible: true,
			status: "error",
			timeout: false,
			position: "tc",
			fixed: false,
		});
	}
	let offlineSnackbar: SnackBar;
	if (!navigator.onLine) {
		setOffline();
	}
	window.addEventListener("offline", () => {
		setOffline();
	});
	function setOffline() {
		setDisableButton(true);
		offlineSnackbar = createSnackBar({
			message: "Du bist offline",
			icon: "!",
			dismissible: false,
			status: "error",
			timeout: false,
			position: "tc",
			fixed: true,
		});
		window.addEventListener(
			"online",
			() => {
				setDisableButton(false);
				changeMonatJahr();
				offlineSnackbar.Close();
				createSnackBar({
					message: "Du bist wieder online",
					dismissible: false,
					status: "info",
					timeout: 2000,
					position: "tc",
					fixed: true,
				});
			},
			{ once: true }
		);
	}

	if (Storage.check("accessToken")) {
		const url = document.location.toString();
		if (url.match("#")) {
			const tab = url.split("#")[1];
			console.log(tab);
			const sel = document.querySelector<HTMLButtonElement>(`#${tab}-tab`);
			if (sel instanceof HTMLButtonElement) {
				console.log(sel);
				Tab.getOrCreateInstance(sel).show();
				window.scrollTo(0, 1);
			}
		}
	}
	Array.from(document.querySelectorAll('[data-bs-toggle="popover"]')).forEach(
		popoverTriggerEl => new Popover(popoverTriggerEl)
	);
});

