const storageAvailable = (type: "localStorage" | "sessionStorage"): boolean => {
	let storage;
	try {
		storage = window[type];
		const x = "__storage_test__";
		storage.setItem(x, x);
		storage.removeItem(x);
		return true;
	} catch (e) {
		if (!storage) return false;
		return (
			e instanceof DOMException &&
			// everything except Firefox
			(e.name === "QuotaExceededError" ||
				// Firefox
				e.name === "NS_ERROR_DOM_QUOTA_REACHED") &&
			// acknowledge QuotaExceededError only if there's something already stored
			storage &&
			storage.length !== 0
		);
	}
};

export async function download(button: HTMLButtonElement | null, modus: "B" | "E" | "N"): Promise<void> {
	if (button === null) return;
	setLoading(button.id);
	buttonDisable(true);

	const MonatInput = document.querySelector<HTMLInputElement>("#Monat");
	const JahrInput = document.querySelector<HTMLInputElement>("#Jahr");

	if (!MonatInput || !JahrInput) throw new Error("Input Element nicht gefunden");

	const VorgabenGeld: IVorgabenGeld = Storage.get("VorgabenGeld");
	Object.setPrototypeOf(VorgabenGeld, {
		getMonat: function (maxMonat: number): IVorgabenGeldType {
			let returnObjekt = VorgabenGeld[1];
			const keys = Object.keys(VorgabenGeld).map(Number);
			if (keys.length > 1 && maxMonat > 1 && Math.max(...keys.filter(key => key <= maxMonat)) > 1) {
				for (let monat = 2; monat <= maxMonat; monat++) {
					if (typeof VorgabenGeld[monat] !== "undefined") returnObjekt = { ...returnObjekt, ...VorgabenGeld[monat] };
				}
			}
			return returnObjekt;
		},
	});
	const Monat = +MonatInput.value;
	const Jahr = +JahrInput.value;
	const data = {
		VorgabenU: Storage.get<IVorgabenU>("VorgabenU"),
		VorgabenGeld: VorgabenGeld.getMonat(Monat),
		Daten: {} as IDaten,
		Monat,
		Jahr,
	};

	switch (modus) {
		case "B":
			data.Daten.BZ = tableToArray("tableBZ");
			data.Daten.BE = tableToArray("tableBE");
			break;
		case "E":
			data.Daten.EWT = tableToArray("tableE");
			break;
		case "N":
			data.Daten.N = tableToArray("tableN");
			break;
		default:
			throw new Error("Modus fehlt");
	}

	try {
		console.time("download");
		const fetched = await FetchRetry<
			{
				VorgabenU: IVorgabenU;
				VorgabenGeld: IVorgabenGeldType;
				Daten: Partial<IDaten>;
				Monat: number;
				Jahr: number;
			},
			{ data: string; name: string }
		>(`download/${modus}`, data, "POST");
		if (fetched instanceof Error) return;
		if (fetched.statusCode != 200) {
			console.error("Fehler", fetched.message);
			createSnackBar({
				message: `Download fehlerhaft:<br/>${fetched.message}`,
				status: "error",
				timeout: 3000,
				position: "br",
				fixed: true,
			});
			return;
		}
		saveAs(convertToBlob(fetched.data.data), fetched.data.name);
	} catch (err) {
		console.error(err);
		return;
	} finally {
		console.timeEnd("download");
		buttonDisable(false);
		clearLoading(button.id);
	}
}

export function convertToBlob(data: string, type = "application/pdf"): Blob {
	const binaryData = window.atob(data);
	const arrayBuffer = new ArrayBuffer(binaryData.length);
	const uint8Array = new Uint8Array(arrayBuffer);

	for (let i = 0; i < binaryData.length; i++) {
		uint8Array[i] = binaryData.charCodeAt(i);
	}

	return new Blob([arrayBuffer], { type });
}

export async function saveDaten(button: HTMLButtonElement | null): Promise<void> {
	if (button === null) return;
	setLoading(button.id);
	buttonDisable(true);

	try {
		const tableBZ = document.querySelector<CustomHTMLTableElement>("#tableBZ");
		const tableBE = document.querySelector<CustomHTMLTableElement>("#tableBE");
		const tableE = document.querySelector<CustomHTMLTableElement>("#tableE");
		const tableN = document.querySelector<CustomHTMLTableElement>("#tableN");

		if (!tableBZ || !tableBE || !tableE || !tableN) {
			throw new Error("Custom table not found");
		}

		const ftBZ = tableBZ.instance;
		const ftBE = tableBE.instance;
		const ftE = tableE.instance;
		const ftN = tableN.instance;

		saveTableData("tableBZ", ftBZ);
		saveTableData("tableBE", ftBE);
		saveTableData("tableE", ftE);
		saveTableData("tableN", ftN);
		saveEinstellungen();

		const data = {
			BZ: Storage.get<IDaten["BZ"]>("dataBZ"),
			BE: Storage.get<IDaten["BE"]>("dataBE"),
			E: Storage.get<IDaten["EWT"]>("dataE"),
			N: Storage.get<IDaten["N"]>("dataN"),
			User: Storage.get<IVorgabenU>("VorgabenU"),
			Monat: Storage.get<number>("Monat"),
			Jahr: Storage.get<number>("Jahr"),
		};

		const fetched = await FetchRetry<
			{
				BZ: IDaten["BZ"];
				BE: IDaten["BE"];
				E: IDaten["EWT"];
				N: IDaten["N"];
				User: IVorgabenU;
				Monat: number;
				Jahr: number;
			},
			{
				datenBerechnung: IVorgabenBerechnung;
				daten: {
					datenBZ: IDaten["BZ"];
					datenBE: IDaten["BE"];
					datenE: IDaten["EWT"];
					datenN: IDaten["N"];
				};
				user: IVorgabenU;
			}
		>("saveData", data, "POST");

		if (fetched.statusCode != 200) {
			console.error("Fehler", fetched.message);
			const messages = JSON.parse(fetched.message);
			createSnackBar({
				message: "Speichern<br/>Es ist ein Fehler aufgetreten:" + messages.map((message: string) => `<br/>- ${message}`),
				status: "error",
				timeout: 3000,
				position: "br",
				fixed: true,
			});

			return;
		}
		const dataResponded = fetched.data;
		console.log(dataResponded);

		generateTableBerechnung(dataResponded.datenBerechnung);

		ftBZ.rows.load(DataBZ(dataResponded.daten.datenBZ));
		console.log("saved ", { ftBZ });

		ftBE.rows.load(DataBE(dataResponded.daten.datenBE));
		console.log("saved ", { ftBE });

		ftE.rows.load(DataE(dataResponded.daten.datenE));
		console.log("saved ", { ftE });

		ftN.rows.load(DataN(dataResponded.daten.datenN));
		console.log("saved ", { ftN });

		saveTableData("tableBZ", ftBZ);
		saveTableData("tableBE", ftBE);
		saveTableData("tableE", ftE);
		saveTableData("tableN", ftN);
		generateEingabeMaskeEinstellungen(dataResponded.user);

		Storage.set("VorgabenU", dataResponded.user);
		Storage.set("datenBerechnung", dataResponded.datenBerechnung);

		console.log("Erfolgreich gespeichert");
		createSnackBar({
			message: `Speichern<br/>Daten gespeichert`,
			status: "success",
			timeout: 3000,
			position: "br",
			fixed: true,
		});
	} catch (err) {
		console.error(err);
		return;
	} finally {
		clearLoading(button.id);
		buttonDisable(false);
	}
}

export function saveTableData(tableID: string, ft: CustomTable): void {
	let data;
	switch (tableID) {
		case "tableBZ":
			data = tableToArray("tableBZ", ft);
			Storage.set("dataBZ", data);
			break;
		case "tableBE":
			data = tableToArray("tableBE", ft);
			Storage.set("dataBE", data);
			break;
		case "tableE":
			data = tableToArray("tableE", ft);
			Storage.set("dataE", data);
			break;
		case "tableN":
			data = tableToArray("tableN", ft);
			Storage.set("dataN", data);
			break;
		case "tableVE":
			data = Storage.get<IVorgabenU>("VorgabenU");
			data.vorgabenB = Object.fromEntries(tableToArray("tableVE", ft).entries()) as IVorgabenU["vorgabenB"];
			Storage.set("VorgabenU", data);
	}
}

export function tableToArray<T>(tableID: string, ft?: CustomTable): T[] {
	if (!ft) {
		const table = document.querySelector<CustomHTMLTableElement>(`#${tableID}`);
		if (!table) throw new Error("table nicht gefunden");
		ft = table.instance;
	}
	return ft.getRows().map((row: Row) => row.cells as T);
}

export function setDisableButton(status: boolean): void {
	const btnAuswaehlen = document.querySelector<HTMLButtonElement>("#btnAuswaehlen");
	if (btnAuswaehlen) {
		btnAuswaehlen.disabled = status;
	}

	const btnChange = document.querySelector<HTMLButtonElement>("#btnChange");
	if (btnChange) {
		btnChange.disabled = status;
	}

	const btnSaveList = document.querySelectorAll<HTMLButtonElement>("[id^='btnSave']");
	btnSaveList.forEach(button => {
		button.disabled = status;
	});

	const btnDownloadList = document.querySelectorAll<HTMLButtonElement>("[id^='btnDownload']");
	btnDownloadList.forEach(button => {
		button.disabled = status;
	});

	const btnPasswortAEndern = document.querySelector<HTMLButtonElement>("#btnPasswortAEndern");
	if (btnPasswortAEndern) {
		btnPasswortAEndern.disabled = status;
	}
}

export function buttonDisable(status: boolean) {
	const buttons = Array.from(document.querySelectorAll<HTMLButtonElement>("[data-disabler]"));
	buttons.forEach(button => (button.disabled = status));
	console.log(`Button disabled: ${status}`);
}

export function setLoading(btn: string) {
	const btnElement = document.querySelector<HTMLButtonElement>(`#${btn}`);
	if (!btnElement) return;
	if (!btnElement.dataset.normaltext) btnElement.dataset.normaltext = btnElement.innerHTML;
	if (!btnElement.dataset.loadingtext)
		btnElement.dataset.loadingtext =
			"<span class='spinner-grow spinner-grow-sm' role='status' aria-hidden='true'></span>";
	btnElement.disabled = true;
	btnElement.innerHTML = btnElement.dataset.loadingtext;
}

export function clearLoading(btn: string) {
	const btnElement = document.querySelector<HTMLButtonElement>(`#${btn}`);
	if (!btnElement) return;
	btnElement.innerHTML = <string>btnElement.dataset.normaltext;
	btnElement.disabled = false;
}

export function DatenSortieren<T extends Record<string, string | number | boolean | dayjs.Dayjs>>(daten: T[]) {
	daten.sort((x: T, y: T) => {
		const xp = x[0];
		const yp = y[0];
		if (xp < yp) {
			return xp == yp ? 0 : -1;
		} else {
			return xp == yp ? 0 : 1;
		}
	});
}

let REFRESHED = 0;

export let controller: AbortController;
let signal: AbortSignal;
newAbortControler();

export function newAbortControler() {
	controller = new AbortController();
	signal = controller.signal;
}

export async function FetchRetry<I, T>(
	UrlPath: string,
	data?: I,
	method: "GET" | "POST" | "UPDATE" = "GET",
	retry = 0,
	accessToken: string = Storage.get("accessToken")
): Promise<{ data: T; status: boolean; statusCode: number; message: string }> {
	if (retry > 2) throw new Error("Zu viele Tokenfehler");
	const lastServerContact = +(sessionStorage.getItem("lastServerContact") as string);
	let serverReady = false;
	if (!lastServerContact || lastServerContact - Date.now() + 5 * 60 * 1000 < 0) {
		const statusSnackBar = createSnackBar({
			message: "Serververbindung wird aufgebaut. Bitte warten.",
			dismissible: false,
			status: "info",
			timeout: false,
			position: "br",
			fixed: true,
		});

		try {
			console.time("Serververbindung herstellen");
			await fetch(`${API_URL}/`, { method: "GET", signal });
			sessionStorage.setItem("lastServerContact", Date.now().toString());
			serverReady = true;
		} catch {
			createSnackBar({
				message: "Server nicht Erreichbar",
				status: "error",
				position: "br",
				fixed: true,
			});
			throw new Error("Server nicht Erreichbar");
		} finally {
			console.timeEnd("Serververbindung herstellen");
			statusSnackBar.Close();
		}
	} else {
		serverReady = true;
	}
	if (!serverReady) throw new Error("Server nicht Erreichbar");
	const headers = new Headers();
	if (method !== "GET") headers.set("Content-Type", "application/json");
	if (accessToken) {
		if (!data || (data && !Object.prototype.hasOwnProperty.call(data, "refreshToken")))
			accessToken = await getValidAccesstoken(accessToken);
		headers.set("Authorization", `Bearer ${accessToken}`);
	}
	const fetchObject: RequestInit = {
		mode: "cors",
		headers,
		method,
		signal,
		cache: "no-cache",
	};
	if (data) {
		fetchObject.body = JSON.stringify(data);
	}

	const response = await fetch(`${API_URL}/${UrlPath}`, fetchObject);
	const responded = await response.json();
	if (response.status == 401 && responded.message == "Token abgelaufen") {
		accessToken = await tokenErneuern(retry, undefined, accessToken);
		return await FetchRetry(UrlPath, data, method, retry + 1, accessToken);
	}
	responded.statusCode = response.status;
	return responded;
}

async function getValidAccesstoken(accesstoken: string = Storage.get("accessToken")) {
	const tokenPayload = decodeAccessToken(accesstoken);
	const expirationTimeInSeconds = tokenPayload.exp * 1000;
	const now = new Date();
	return expirationTimeInSeconds < now.getTime() ? await tokenErneuern() : accesstoken;
}

export function decodeAccessToken(accesstoken: string = Storage.get("accessToken")): {
	id: string;
	Name: string;
	Berechtigung: number;
	iat: number;
	exp: number;
} {
	const base64Url = accesstoken.split(".")[1];
	const base64 = base64Url.replace(/-/g, "+").replace(/_/g, "/");
	const jsonPayload = decodeURIComponent(
		window
			.atob(base64)
			.split("")
			.map(function (c) {
				return "%" + ("00" + c.charCodeAt(0).toString(16)).slice(-2);
			})
			.join("")
	);
	return JSON.parse(jsonPayload);
}

async function tokenErneuern(
	retry?: number,
	refreshToken: string = Storage.get("refreshToken"),
	accessToken: string = Storage.get("accessToken")
) {
	if (!refreshToken || !accessToken || REFRESHED > 2) {
		REFRESHED = 0;
		Logout();
		createSnackBar({
			message: `Login<br/>Fehlerhafte Anmeldung,</br> bitte Erneut anmelden!`,
			status: "error",
			timeout: 3000,
			position: "br",
			fixed: true,
		});
		throw new Error("Fehler bei Token erneuerung");
	}
	const responded = await FetchRetry<{ refreshToken: string }, { accessToken: string; refreshToken: string }>(
		"refreshToken",
		{ refreshToken },
		"POST",
		retry,
		accessToken
	);
	if (responded instanceof Error) throw new Error("Fehler");
	if (responded.statusCode === 200) {
		REFRESHED++;
		const { accessToken, refreshToken } = responded.data;
		console.log({ accessToken, refreshToken });
		Storage.set("accessToken", accessToken);
		Storage.set("refreshToken", refreshToken);
		return accessToken;
	} else {
		Logout();
		createSnackBar({
			message: `Login<br/>Fehlerhafte Anmeldung,</br> bitte Erneut anmelden!`,
			status: "error",
			timeout: 3000,
			position: "br",
			fixed: true,
		});
		throw new Error("Fehler bei Token erneuerung");
	}
}

export function isObject(value: unknown): boolean {
	return (
		typeof value === "object" && !Array.isArray(value) && Object.prototype.toString.call(value) === "[object Object]"
	);
}

export function checkMaxTag(Jahr: number, Monat: number): number {
	return dayjs([Jahr, Monat, dayjs().date()]).month() === Monat ? dayjs().date() : 1;
}

export function toJSON<T = unknown>(formData: FormData, table: CustomTable): T {
	const nameConvert: Record<string, string> = table.columns.array
		.filter(column => column.name !== "editing")
		.reduce((nameConvertMap, column) => {
			nameConvertMap[column.title] = column.name;
			return nameConvertMap;
		}, {} as Record<string, string>);
	const output: Record<string, unknown> = {};
	formData.forEach((value, key) => {
		console.log(key, value);
		const fieldName = nameConvert[key];
		if (!fieldName) return;
		if (output[fieldName]) {
			if (Array.isArray(output[fieldName])) {
				(output[fieldName] as unknown[]).push(value);
			} else {
				output[fieldName] = [output[fieldName], value];
			}
		} else {
			output[fieldName] = value;
		}
	});
	return output as T;
}
