import { Modal } from "bootstrap";
import { BereitschaftsEinsatzZeitrume } from "./Bereitschaft.js";
import { buttonDisable, saveDaten, saveTableData, Storage, tableToArray } from "./Core.js";
import { createSnackBar } from "./CustomSnackbar";
import { Column, createCustomTable, CustomTable, Row } from "./CustomTable";
import { CustomHTMLDivElement, IVorgabenU } from "./Interfaces";
import {
	createEditorModalFooter,
	createModal,
	createModalBodyCheckbox,
	createModalBodyInputElement,
	createModalBodySelectElement,
	createModalBodyShowElement,
	createShowModalFooter,
} from "./ModalComponents.js";

window.addEventListener("load", () => {
	document.querySelector<HTMLFormElement>("#formEinstellungen")?.addEventListener("submit", e => {
		e.stopPropagation();
		e.preventDefault();
		saveDaten(document.querySelector<HTMLButtonElement>("#btnSaveEinstellungen"));
	});

	if (Storage.check("VorgabenU")) {
		generateEingabeMaskeEinstellungen();
	} else {
		generateEingabeTabelleEinstellungenVorgabenB({});
	}
});

export function generateEingabeMaskeEinstellungen(VorgabenU: IVorgabenU = Storage.get("VorgabenU")) {
	const VorgabenB = VorgabenU.vorgabenB ?? BereitschaftsEinsatzZeitrume;

	for (const key in VorgabenU.pers) {
		const element = document.querySelector<HTMLInputElement | HTMLSelectElement>(`#${key}`);
		const value = VorgabenU.pers[key as keyof IVorgabenU["pers"]];
		if (element instanceof HTMLInputElement || element instanceof HTMLSelectElement) {
			element.value = value.toString();
		}
	}

	for (const key in VorgabenU.aZ) {
		const element = document.querySelector<HTMLInputElement>(`#${key}`);
		const value = VorgabenU.aZ[key as keyof IVorgabenU["aZ"]];
		if (element instanceof HTMLInputElement) {
			element.value = value;
		}
	}

	const tbody = document.querySelector<HTMLTableElement>("#TbodyTtigkeitssttten");
	if (tbody === null) throw new Error();
	tbody.innerHTML = "";

	let td: HTMLTableCellElement, input: HTMLInputElement;
	for (const { key, text, value } of VorgabenU.fZ) {
		const tr = tbody.insertRow();
		createInput(tr, 0, "Text", key);
		createInput(tr, 1, "Text", text);
		createInput(tr, 2, "time", value);
	}

	for (let i = 0; i < 3; i++) {
		const tr = tbody.insertRow();
		createInput(tr, 0, "Text", "");
		createInput(tr, 1, "Text", "");
		createInput(tr, 2, "time", "");
	}

	function createInput(tr: HTMLTableRowElement, position: number, type: "Text" | "time", value: string) {
		td = tr.insertCell(position);
		input = document.createElement("input");
		input.type = type;
		input.className = "form-control text-center";
		input.value = value;
		td.appendChild(input);
	}

	generateEingabeTabelleEinstellungenVorgabenB(VorgabenB);
}

function generateEingabeTabelleEinstellungenVorgabenB(VorgabenB?: IVorgabenU["vorgabenB"]) {
	if (!VorgabenB) {
		VorgabenB = Storage.check("VorgabenU") ? Storage.get<IVorgabenU>("VorgabenU").vorgabenB : {};
	}

	const trueParser = (value: boolean | null): string => {
		return value ? "Ja" : "Nein";
	};
	const addWeekParser = (value: { tag: number; zeit: string; Nwoche?: boolean }, umbruchString: string): string => {
		if (value.Nwoche === undefined || value.Nwoche === false) return `${umbruchString} - `;
		return `${umbruchString}+1 Woche`;
	};
	const weekdayParser = (value: { tag: number; zeit: string; Nwoche?: boolean }, umbruch = true): string => {
		const umbruchString: string = umbruch ? "<br/>" : " | ";
		switch (value.tag % 7) {
			case 1:
				return `Montag${addWeekParser(value, umbruchString)}${umbruchString}${value.zeit}`;
			case 2:
				return `Dienstag${addWeekParser(value, umbruchString)}${umbruchString}${value.zeit}`;
			case 3:
				return `Mittwoch${addWeekParser(value, umbruchString)}${umbruchString}${value.zeit}`;
			case 4:
				return `Donnerstag${addWeekParser(value, umbruchString)}${umbruchString}${value.zeit}`;
			case 5:
				return `Freitag${addWeekParser(value, umbruchString)}${umbruchString}${value.zeit}`;
			case 6:
				return `Samstag${addWeekParser(value, umbruchString)}${umbruchString}${value.zeit}`;
			case 0:
			case 7:
				return `Sonntag${addWeekParser(value, umbruchString)}${umbruchString}${value.zeit}`;
			default:
				return "";
		}
	};
	const ftVE = createCustomTable("tableVE", {
		columns: [
			{ name: "Name", title: "Name" },
			{ name: "standard", title: "Standard", parser: trueParser, breakpoints: "md" },
			{ name: "beginnB", title: "Von", parser: weekdayParser, breakpoints: "sm" },
			{ name: "endeB", title: "Bis", parser: weekdayParser, breakpoints: "sm" },
			{ name: "nacht", title: "Nacht?", parser: trueParser, breakpoints: "lg" },
			{ name: "beginnN", title: "Von", parser: weekdayParser, breakpoints: "lg" },
			{ name: "endeN", title: "Bis", parser: weekdayParser, breakpoints: "lg" },
		],
		rows: [...Object.values(VorgabenB)],
		editing: {
			enabled: true,
			addRow: () => {
				const $modal = createEditorModalVE(ftVE, "Voreinstellung hinzufgen");
				$modal.row = ftVE;
				new Modal($modal).show();
			},
			editRow: (row: Row) => {
				const $modal = createEditorModalVE(row, "Voreinstellung bearbeiten");
				$modal.row = row;
				new Modal($modal).show();
			},
			showRow: (row: Row) => {
				const $modal = createShowModalVE(row, "Voreinstellung anzeigen");
				$modal.row = row;
				new Modal($modal).show();
			},
			deleteRow: (row: Row) => {
				if (!row.cells.standard) {
					row.deleteRow();
				} else {
					createSnackBar({
						message: "Lschen von Standard nicht mglich<br /><small>(Bitte erst neuen Standart setzten)</small>",
						icon: "!",
						status: "info",
						dismissible: true,
						timeout: 3000,
						position: "br",
						fixed: true,
					});
				}
			},
			deleteAllRows: () => {
				createSnackBar({
					message: "Mchtest du wirklich alle Zeilen lschen?",
					icon: "question",
					status: "error",
					dismissible: false,
					timeout: false,
					position: "br",
					fixed: true,
					actions: [
						{
							text: "Ja",
							function: () => {
								ftVE.rows.load([]);
								buttonDisable(false);
							},
							dismiss: true,
							class: ["text-danger"],
						},
						{ text: "Nein", dismiss: true, class: ["text-primary"] },
					],
				});
			},
		},
	});
}

function createEditorModalVE(row: Row | CustomTable, titel: string): CustomHTMLDivElement {
	const { modal, form } = createModal(
		titel,
		true,
		null,
		createBodyElement,
		createEditorModalFooter(null, row instanceof Row ? "Speichern" : null),
		SubmitEventListener
	);
	return modal;

	function createBodyElement(): HTMLDivElement {
		const modalBody = document.createElement("div");
		modalBody.className = "modal-body row";
		let column;
		column = row.columns.array.find((column: Column) => column.name === "Name") as Column;
		modalBody.appendChild(
			createModalBodyInputElement({
				divClass: "form-floating col-12 pb-3",
				title: column.title,
				name: column.name,
				value: row instanceof Row ? row.cells && row.cells[column.name] : "",
				type: "text",
				required: true,
			})
		);

		column = row.columns.array.find((column: Column) => column.name === "standard") as Column;
		modalBody.appendChild(
			createModalBodyCheckbox({
				checkClass: "form-check form-switch col-12 pb-3",
				id: column.name,
				text: column.title,
				status: row instanceof Row ? row.cells && row.cells[column.name] : false,
			})
		);

		modalBody.appendChild(document.createElement("hr"));

		["beginnB", "endeB"].forEach(value => {
			column = row.columns.array.find((column: Column) => column.name === value) as Column;
			modalBody.appendChild(
				createModalBodySelectElement({
					divClass: `form-floating col-7 col-sm-6 ${value === "beginnB" ? "pb-3" : ""}`,
					title: column.title,
					name: `${column.name}Tag`,
					value: row instanceof Row ? row.cells && row.cells[column.name].tag : "",
					required: true,
					options: [
						{ value: 1, text: "Montag" },
						{ value: 2, text: "Dienstag" },
						{ value: 3, text: "Mittwoch" },
						{ value: 4, text: "Donnerstag" },
						{ value: 5, text: "Freitag" },
						{ value: 6, text: "Samstag" },
						{ value: 0, text: "Sonntag" },
					],
				})
			);
			modalBody.appendChild(
				createModalBodyInputElement({
					divClass: `form-floating col-5 col-sm-6 ${value === "beginnB" ? "pb-3" : ""}`,
					type: "time",
					title: column.title,
					name: `${column.name}Zeit`,
					value: row instanceof Row ? row.cells && row.cells[column.name].zeit : "",
					required: true,
				})
			);
			if (value === "endeB") {
				modalBody.appendChild(
					createModalBodyCheckbox({
						checkClass: "form-check form-switch col-12 pb-3",
						id: `${column.name}Nwoche`,
						text: '+1 Woche? <span class="text-secondary text-opacity-75">(Sonntag - Samstag)</span>',
						status: row instanceof Row ? row.cells && row.cells[column.name].Nwoche : false,
					})
				);
			}
		});

		modalBody.appendChild(document.createElement("hr"));

		column = row.columns.array.find((column: Column) => column.name === "nacht") as Column;
		modalBody.appendChild(
			createModalBodyCheckbox({
				checkClass: "form-check form-switch col-12 pb-3",
				id: column.name,
				text: column.title,
				status: row instanceof Row ? row.cells && row.cells[column.name] : false,
			})
		);

		["beginnN", "endeN"].forEach(value => {
			column = row.columns.array.find((column: Column) => column.name === value) as Column;
			modalBody.appendChild(
				createModalBodySelectElement({
					divClass: "form-floating col-7 col-sm-6",
					title: column.title,
					name: `${column.name}Tag`,
					value: row instanceof Row ? row.cells && row.cells[column.name].tag : "",
					required: true,
					options: [
						{ value: 1, text: "Montag" },
						{ value: 2, text: "Dienstag" },
						{ value: 3, text: "Mittwoch" },
						{ value: 4, text: "Donnerstag" },
						{ value: 5, text: "Freitag" },
						{ value: 6, text: "Samstag" },
						{ value: 0, text: "Sonntag" },
					],
				})
			);
			modalBody.appendChild(
				createModalBodyInputElement({
					divClass: "form-floating col-5 col-sm-6",
					type: "time",
					title: column.title,
					name: `${column.name}Zeit`,
					value: row instanceof Row ? row.cells && row.cells[column.name].zeit : "",
					required: true,
				})
			);
			modalBody.appendChild(
				createModalBodyCheckbox({
					checkClass: "form-check form-switch col-12 pb-3",
					id: `${column.name}Nwoche`,
					text: '+1 Woche? <span class="text-secondary text-opacity-75">(Sonntag - Samstag)</span>',
					status: row instanceof Row ? row.cells && row.cells[column.name].Nwoche : false,
				})
			);
		});

		return modalBody;
	}

	function SubmitEventListener(): (event: Event) => void {
		return (event: Event) => {
			if (form instanceof HTMLDivElement) return;
			if (form.checkValidity && !form.checkValidity()) return;
			event.preventDefault();

			const row = modal.row;
			if (!row) throw new Error("Row nicht gefunden");
			const table = row instanceof Row ? row.CustomTable : row;

			const beginnBTag = Number(form.querySelector<HTMLInputElement>("#beginnBTag")?.value ?? NaN);
			const endeBTag = Number(form.querySelector<HTMLInputElement>("#endeBTag")?.value ?? NaN);
			const beginnNTag = Number(form.querySelector<HTMLInputElement>("#beginnNTag")?.value ?? NaN);
			const endeNTag = Number(form.querySelector<HTMLInputElement>("#endeNTag")?.value ?? NaN);
			const endeBNwoche = form.querySelector<HTMLInputElement>("#endeBNwoche")?.checked ?? false;
			const beginnNNwoche = form.querySelector<HTMLInputElement>("#beginnNNwoche")?.checked ?? false;
			const endeNNwoche = form.querySelector<HTMLInputElement>("#endeNNwoche")?.checked ?? false;
			const beginnBZeit = form.querySelector<HTMLInputElement>("#beginnBZeit")?.value ?? "";
			const endeBZeit = form.querySelector<HTMLInputElement>("#endeBZeit")?.value ?? "";
			const beginnNZeit = form.querySelector<HTMLInputElement>("#beginnNZeit")?.value ?? "";
			const endeNZeit = form.querySelector<HTMLInputElement>("#endeNZeit")?.value ?? "";
			const nameN = form.querySelector<HTMLInputElement>("#Name")?.value ?? "";
			const nacht = form.querySelector<HTMLInputElement>("#nacht")?.checked ?? false;
			const standard = form.querySelector<HTMLInputElement>("#standard")?.checked ?? false;

			const values: IVorgabenU["vorgabenB"][0] = {
				Name: nameN,
				beginnB: { tag: beginnBTag, zeit: beginnBZeit },
				endeB: { tag: endeBTag, zeit: endeBZeit, Nwoche: endeBNwoche },
				nacht: nacht,
				beginnN: { tag: beginnNTag, zeit: beginnNZeit, Nwoche: beginnNNwoche },
				endeN: { tag: endeNTag, zeit: endeNZeit, Nwoche: endeNNwoche },
				standard: standard ? true : undefined,
			};
			if (standard) {
				let ft: CustomTable;
				let newStandard: Row | null;
				if (row instanceof Row) {
					[ft, newStandard] = [row.CustomTable, row];
				} else if (row instanceof CustomTable) {
					[ft, newStandard] = [row, null];
				} else {
					throw new Error("CustomTable nicht gefunden");
				}
				setStandard(ft, newStandard);
			}
			row instanceof Row ? row.val(values) : row.rows.add(values);

			(<Modal>Modal.getInstance(modal)).hide();
			saveTableData("tableVE", table);
		};

		function setStandard(ft: CustomTable, newStandard: Row | null): void {
			const rows = ft.getRows();

			for (const key in rows) {
				const value = rows[key].cells;
				if (newStandard && newStandard === rows[key]) {
					value.standard = true;
					rows[key].val(value);
				} else {
					if (value.standard) {
						delete value.standard;
						rows[key].val(value);
					}
				}
			}
		}
	}
}

function createShowModalVE(row: Row, title: string): CustomHTMLDivElement {
	const { modal } = createModal(title, false, null, createModalBody, createShowModalFooter);
	return modal;

	function createModalBody(): HTMLDivElement {
		const modalBody = document.createElement("div");
		modalBody.className = "modal-body row";
		let column: Column;

		column = row.columns.array.find((column: Column) => column.name === "Name") as Column;
		modalBody.appendChild(
			createModalBodyShowElement({
				divClass: "mb-1",
				labelClass: "col-3 col-form-label text-wrap fw-bold",
				spanClass: "col-9 align-middle text-break my-auto",
				title: `${column.title}:`,
				name: column.name,
				text: column.parser(row.cells[column.name]),
			})
		);

		column = row.columns.array.find((column: Column) => column.name === "standard") as Column;
		modalBody.appendChild(
			createModalBodyShowElement({
				divClass: "mb-1",
				labelClass: "col-3 col-form-label text-wrap fw-bold",
				spanClass: "col-9 align-middle text-break my-auto",
				title: `${column.title}:`,
				name: column.name,
				text: column.parser(row.cells[column.name]),
			})
		);

		["beginnB", "endeB", "nacht", "beginnN", "endeN"].forEach(value => {
			column = row.columns.array.find((column: Column) => column.name === value) as Column;
			modalBody.appendChild(
				createModalBodyShowElement({
					labelClass: "col-3 col-form-label text-wrap fw-bold",
					spanClass: "col-9 align-middle text-break my-auto",
					title: `${column.title}:`,
					name: column.name,
					text: column.parser(row.cells[column.name], false),
				})
			);
		});

		return modalBody;
	}
}

export function saveEinstellungen(): void {
	const VorgabenU: IVorgabenU = Storage.get("VorgabenU");

	function updatePersonal<T extends keyof IVorgabenU["pers"]>(key: T, value: IVorgabenU["pers"][T]): void {
		VorgabenU.pers[key] = value;
	}

	for (const [key, value] of Object.entries(VorgabenU.pers)) {
		updatePersonal(key as keyof IVorgabenU["pers"], value);
	}

	Object.keys(VorgabenU.aZ).forEach(key => {
		const input = document.querySelector<HTMLInputElement>(`#${key}`);
		if (input) VorgabenU.aZ[key as keyof IVorgabenU["aZ"]] = input.value;
	});

	VorgabenU.fZ = table_to_array_einstellungen("TbodyTtigkeitssttten");

	if (VorgabenU.pers.TB == "Tarifkraft") {
		(<HTMLLIElement>document.querySelector<HTMLDivElement>("#Neben-tab")?.parentElement).classList.remove("d-none");
	} else {
		(<HTMLLIElement>document.querySelector<HTMLDivElement>("#Neben-tab")?.parentElement).classList.add("d-none");
	}
	VorgabenU.vorgabenB = Object.fromEntries(tableToArray("tableVE").entries()) as IVorgabenU["vorgabenB"];

	Storage.set("VorgabenU", VorgabenU);

	function table_to_array_einstellungen(table_id: string): { key: string; text: string; value: string }[] | [] {
		const myData = document.querySelector<HTMLTableElement>(`#${table_id}`)?.rows;
		if (!myData) return [];
		const my_liste: { key: string; text: string; value: string }[] = [];
		for (const myDatum of Array.from(myData)) {
			const el = myDatum.children;
			const key: string = (<HTMLInputElement>el[0].children[0]).value;
			if (!key) {
				continue;
			}
			const text: string = (<HTMLInputElement>el[1].children[0]).value;
			const value: string = (<HTMLInputElement>el[2].children[0]).value;
			if (!text || !value) {
				createSnackBar({
					message: `Einstellungen > Fahrzeiten > "${key}": Beschreibung / Fahrzeit fehlt`,
					status: "error",
					timeout: 3000,
					position: "br",
					fixed: true,
				});
				throw new Error("Beschreibung / Fahrzeit fehlt");
			}
			my_liste.push({ key, text, value });
		}

		return my_liste;
	}
}
