import { Row } from "./CustomTable";
import { CustomHTMLDivElement } from "./Interfaces";

export function createEditorModalFooter(
	customButtons?: Array<HTMLButtonElement> | null,
	submitText?: string | null
): () => HTMLDivElement {
	return () => {
		const modalFooter = document.createElement("div");
		modalFooter.className = "modal-footer";

		const btnSubmit = document.createElement("button");
		btnSubmit.type = "Submit";
		btnSubmit.className = "btn btn-primary";
		btnSubmit.ariaLabel = submitText ?? "Hinzufgen";
		btnSubmit.innerHTML = submitText ?? "Hinzufgen";
		modalFooter.appendChild(btnSubmit);

		if (customButtons) customButtons.forEach(button => modalFooter.appendChild(button));

		const btnClose = document.createElement("button");
		btnClose.type = "button";
		btnClose.className = "btn btn-secondary";
		btnSubmit.ariaLabel = "Abbrechen";
		btnClose.dataset.bsDismiss = "modal";
		btnClose.textContent = "Abbrechen";
		modalFooter.appendChild(btnClose);

		return modalFooter;
	};
}
export function createShowModalFooter(): HTMLDivElement {
	const modalFooter = document.createElement("div");
	modalFooter.className = "modal-footer";

	const btnEdit = document.createElement("button");
	btnEdit.type = "button";
	btnEdit.className = "btn btn-primary";
	btnEdit.ariaLabel = "editieren";
	btnEdit.dataset.bsDismiss = "modal";
	btnEdit.innerHTML = "Bearbeiten";
	btnEdit.addEventListener("click", event => {
		const row = <Row>(<Required<CustomHTMLDivElement>>(<HTMLButtonElement>event.target).closest(".modal")).row;
		row.CustomTable.options.editing.editRow(row);
	});
	modalFooter.appendChild(btnEdit);

	const btnDelete = document.createElement("button");
	btnDelete.type = "button";
	btnDelete.className = "btn btn-danger";
	btnDelete.ariaLabel = "lschen";
	btnDelete.dataset.bsDismiss = "modal";
	btnDelete.innerHTML = "Lschen";
	btnDelete.addEventListener("click", event => {
		const row = <Row>(<Required<CustomHTMLDivElement>>(<HTMLButtonElement>event.target).closest(".modal")).row;
		row.CustomTable.options.editing.deleteRow(row);
	});
	modalFooter.appendChild(btnDelete);

	const btnClose = document.createElement("button");
	btnClose.type = "button";
	btnClose.className = "btn btn-secondary";
	btnClose.ariaLabel = "Schlieen";
	btnClose.dataset.bsDismiss = "modal";
	btnClose.textContent = "Schlieen";
	modalFooter.appendChild(btnClose);

	return modalFooter;
}

export function createModalBodyShowElement(options: {
	divClass?: string | null;
	labelClass?: string | null;
	spanClass?: string | null;
	title: string;
	name: string;
	text: string | number;
}): HTMLDivElement {
	const divElement = document.createElement("div");
	divElement.className = options.divClass ?? "mb-1 row";

	const label = document.createElement("label");
	label.className = options.labelClass ?? "col-3 col-form-label text-wrap fw-bold";
	label.setAttribute("for", options.name);
	label.textContent = options.title;
	divElement.appendChild(label);

	const span = document.createElement("span");
	span.className = options.spanClass ?? "col-9 align-middle text-break my-auto";
	span.id = options.name;
	span.innerHTML = String(options.text) ?? "&nbsp;";
	divElement.appendChild(span);

	return divElement;
}
export function createModalBodyInputElement(options: {
	type: string;
	name: string;
	title: string;
	value: string | number | Date | null;
	divClass: string;
	required?: boolean;
	disabled?: boolean;
	min?: string | number | Date;
	max?: string | number | Date;
	eventListener?: (this: HTMLElement, ev: Event) => void;
}): HTMLDivElement {
	const div = document.createElement("div");
	div.className = options.divClass;
	if (options.required) div.classList.add("required");

	const input = document.createElement("input");
	input.type = options.type;
	input.className = "form-control validate";
	input.id = options.name;
	input.name = options.title;
	input.value = String(options.value);
	if (options.required) input.required = true;
	if (options.disabled) input.disabled = true;
	if (options.min != undefined) input.min = String(options.min);
	if (options.max != undefined) input.max = String(options.max);
	if (options.eventListener) input.addEventListener("change", options.eventListener);

	div.appendChild(input);

	const label = document.createElement("label");
	label.htmlFor = options.name;
	label.textContent = options.title;
	div.appendChild(label);

	return div;
}
export function createModalBodySelectElement(options: {
	name: string;
	title: string;
	value: string | number | Date | null;
	divClass: string;
	required?: boolean;
	eventListener?: (this: HTMLElement, ev: Event) => void;
	options: { value: string | number; text: string; disabled?: boolean; selected?: boolean; html?: boolean }[];
}): HTMLDivElement {
	const div = document.createElement("div");
	div.className = options.divClass;
	if (options.required) div.classList.add("required");

	const select = document.createElement("select");
	select.className = "form-select validate";
	select.id = options.name;
	if (options.required) select.required = true;
	options.options.forEach(optionObject => {
		const option = document.createElement("option");
		option.value = String(optionObject.value);
		if (optionObject.html) {
			option.innerHTML = optionObject.text;
		} else {
			option.textContent = optionObject.text;
		}
		if (optionObject.disabled) option.disabled = true;
		if (optionObject.selected && !options.value) option.selected = true;
		if (+option.value === options.value) option.selected = true;

		select.appendChild(option);
	});
	if (options.value) select.value = String(options.value);
	if (options.eventListener) select.addEventListener("change", options.eventListener);
	div.appendChild(select);

	const label = document.createElement("label");
	label.className = "form-label";
	label.htmlFor = options.name;
	label.textContent = options.title;
	div.appendChild(label);
	return div;
}
export function createModalBodyCheckbox(options: {
	checkClass: string;
	id: string;
	text: string;
	status?: boolean;
	disabled?: boolean;
	eventListener?: (this: HTMLElement, ev: Event) => void;
}): HTMLDivElement {
	const div = document.createElement("div");
	div.className = options.checkClass;

	const input = document.createElement("input");
	input.type = "checkbox";
	input.className = "form-check-input";
	input.id = options.id;
	if (options.status) input.checked = true;
	if (options.disabled) input.disabled = true;
	if (options.eventListener != undefined) input.addEventListener("change", options.eventListener);
	div.appendChild(input);

	const label = document.createElement("label");
	label.className = "form-check-label";
	label.htmlFor = options.id;
	label.innerHTML = options.text;
	div.appendChild(label);
	return div;
}
export function createModalBodyFillerElement(): HTMLDivElement {
	const div = document.createElement("div");
	div.className = "w-100";
	return div;
}
export function createModalBodyTitelElement(text: string): HTMLHeadingElement {
	const title = document.createElement("h4");
	title.className = "text-center mb-1";
	title.innerHTML = text;
	return title;
}
export function createModalBodySpanElement(spanClass: string, text: string): HTMLSpanElement {
	const span = document.createElement("span");
	span.className = spanClass;
	span.textContent = text;
	return span;
}

export function createModal(
	title: string,
	withForm: boolean,
	size:
		| null
		| "sm"
		| "lg"
		| "xl"
		| "fullscreen"
		| "fullscreen-sm-down"
		| "fullscreen-md-down"
		| "fullscreen-lg-down"
		| "fullscreen-xl-down"
		| "fullscreen-xxl-down",
	createModalBody: () => HTMLDivElement,
	createModalFooter: () => HTMLDivElement,
	SubmitEventListener: () => (event: Event) => void = () => (event: Event) => {
		throw new Error("SubmitEvent fehlt" + event);
	}
): { modal: CustomHTMLDivElement; form: HTMLFormElement | HTMLDivElement } {
	const modal = document.querySelector<CustomHTMLDivElement>("#modal");
	if (modal === null) throw new Error("Element nicht gefunden");
	modal.innerHTML = "";
	modal.row = null;
	modal.role = "document";

	const modalDialog = document.createElement("div");
	modalDialog.className = "modal-dialog";
	if (size) modalDialog.classList.add(`modal-${size}`);

	const ModalContentWrapper = withForm ? document.createElement("form") : document.createElement("div");
	if (withForm && SubmitEventListener) ModalContentWrapper.addEventListener("submit", SubmitEventListener());
	ModalContentWrapper.className = "modal-content";

	ModalContentWrapper.appendChild(createModalHeader(title));
	ModalContentWrapper.appendChild(createModalBody());
	ModalContentWrapper.appendChild(createModalFooter());

	modalDialog.appendChild(ModalContentWrapper);

	modal.appendChild(modalDialog);
	return { modal, form: ModalContentWrapper };

	function createModalHeader(title: string): HTMLDivElement {
		const modalHeader = document.createElement("div");
		modalHeader.className = "modal-header";

		const modalTitle = document.createElement("h5");
		modalTitle.className = "modal-title";
		modalTitle.textContent = title;
		modalHeader.appendChild(modalTitle);

		const btnClose = document.createElement("button");
		btnClose.type = "button";
		btnClose.className = "btn-close";
		btnClose.dataset.bsDismiss = "modal";
		btnClose.ariaLabel = "Schlieen";
		modalHeader.appendChild(btnClose);

		return modalHeader;
	}
}
