import { Modal } from "bootstrap";
import dayjs from "dayjs";
import "dayjs/locale/de.js";
import arraySupport from "dayjs/plugin/arraySupport.js";
import customParseFormat from "dayjs/plugin/customParseFormat.js";
import duration from "dayjs/plugin/duration.js";
import isoWeek from "dayjs/plugin/isoWeek.js";
import LocalizedFormat from "dayjs/plugin/localizedFormat.js";
import {
	DatenSortieren,
	Storage,
	buttonDisable,
	checkMaxTag,
	download,
	saveDaten,
	saveTableData,
	tableToArray,
} from "./Core";
import { createSnackBar } from "./CustomSnackbar";
import { Column, CustomTable, Row, createCustomTable } from "./CustomTable";
import {
	CustomHTMLDivElement,
	CustomHTMLTableElement,
	CustomHTMLTableRowElement,
	IDaten,
	IVorgabenE,
	IVorgabenU,
} from "./Interfaces";
import {
	createEditorModalFooter,
	createModal,
	createModalBodyCheckbox,
	createModalBodyInputElement,
	createModalBodySelectElement,
	createModalBodyShowElement,
	createModalBodySpanElement,
	createModalBodyTitelElement,
	createShowModalFooter,
} from "./ModalComponents";
dayjs.locale("de");
dayjs.extend(arraySupport);
dayjs.extend(customParseFormat);
dayjs.extend(LocalizedFormat);
dayjs.extend(isoWeek);
dayjs.extend(duration);

window.addEventListener("load", () => {
	const berechnenParser = (value: boolean): string => {
			return value
				? '<div class="form-check form-switch"><input type="checkbox" class="row-checkbox form-check-input" checked></div>'
				: '<div class="form-check form-switch"><input type="checkbox" class="row-checkbox form-check-input"></div>';
		},
		schichtParser = (value: string): string => {
			switch (value) {
				case "T":
					return "Tag";
				case "N":
					return "Nacht";
				case "BN":
					return "<span class='SchichtBereitschaft'>Bereitschaft<br>+ Nacht</span>";
				case "S":
					return "Sonder";
				default:
					return "";
			}
		},
		ftE = createCustomTable("tableE", {
			columns: [
				{ name: "tagE", title: "Tag", sortable: true, sorted: true, direction: "ASC" },
				{ name: "eOrtE", title: "Einsatzort", classes: ["custom-text-truncate"], type: "text" },
				{ name: "schichtE", title: "Schicht", parser: schichtParser, type: "time" },
				{ name: "abWE", title: "Ab Wohnung", breakpoints: "xl", type: "time" },
				{ name: "beginE", title: "Arbeitszeit Von", breakpoints: "md", type: "time" },
				{ name: "ab1E", title: "Ab 1.Tgk.-St.", breakpoints: "lg", type: "time" },
				{ name: "anEE", title: "An Einsatzort", breakpoints: "lg", type: "time" },
				{ name: "abEE", title: "Ab Einsatzort", breakpoints: "lg", type: "time" },
				{ name: "an1E", title: "An 1.Tgk.-St.", breakpoints: "lg", type: "time" },
				{ name: "endeE", title: "Arbeitszeit Bis", breakpoints: "md", type: "time" },
				{ name: "anWE", title: "An Wohnung", breakpoints: "xl", type: "time" },
				{ name: "berechnen", title: "Berechnen?", parser: berechnenParser, breakpoints: "xxl" },
			],
			rows: DataE(),
			sorting: { enabled: true },
			editing: {
				enabled: true,
				addRow: () => {
					const $modal = createEditorModalEWT(ftE, "Anwesenheit hinzufgen");
					$modal.row = ftE;
					new Modal($modal).show();
				},
				editRow: row => {
					const $modal = createEditorModalEWT(row, "Anwesenheit bearbeiten");
					$modal.row = row;
					new Modal($modal).show();
				},
				showRow: row => {
					const $modal = createShowModalEWT(row, "Anwesenheit anzeigen");
					$modal.row = row;
					new Modal($modal).show();
				},
				deleteRow: row => {
					row.deleteRow();
					saveTableData("tableE", ftE);
				},
				deleteAllRows: () => {
					createSnackBar({
						message: "Mchtest du wirklich alle Zeilen lschen?",
						icon: "question",
						status: "error",
						dismissible: false,
						timeout: false,
						position: "br",
						fixed: true,
						actions: [
							{
								text: "Ja",
								function: () => {
									ftE.rows.load([]);
									buttonDisable(false);
									Storage.set("dataE", []);
								},
								dismiss: true,
								class: ["text-danger"],
							},
							{ text: "Nein", dismiss: true, class: ["text-primary"] },
						],
					});
				},
				customButton: [
					{
						classes: ["btn", "btn-secondary"],
						text: "Alle Zeiten entfernen",
						function: () => {
							createSnackBar({
								message:
									"Mchtest du wirklich alle Zeiten entfernen?<br /><small>(nur bei Zeilen die auch berechnet werden)</small>",
								icon: "question",
								status: "error",
								dismissible: false,
								timeout: false,
								position: "br",
								fixed: true,
								actions: [
									{
										text: "Ja",
										function: () => {
											const rows = [...ftE.rows.array];
											const newRows: { [key: string]: unknown }[] = [];
											rows.forEach(row => {
												if (!row.cells.berechnen) return newRows.push(row.cells);
												row.cells.abWE = "";
												row.cells.ab1E = "";
												row.cells.anEE = "";
												row.cells.beginE = "";
												row.cells.endeE = "";
												row.cells.abEE = "";
												row.cells.an1E = "";
												row.cells.anWE = "";
												return newRows.push(row.cells);
											});
											ftE.rows.load(newRows);
											saveTableData("tableE", ftE);
										},
										dismiss: true,
										class: ["text-danger"],
									},
									{ text: "Nein", dismiss: true, class: ["text-primary"] },
								],
							});
						},
					},
				],
			},
			customFunction: {
				afterDrawRows: addEventlistenerToggleBerechnen,
			},
		});

	const btnZb = document.querySelector<HTMLButtonElement>("#btnZb");
	if (btnZb) {
		btnZb.addEventListener("click", () => {
			ewtBerechnen({
				monat: Storage.get<number>("Monat"),
				jahr: Storage.get<number>("Jahr"),
				daten: Storage.get<IDaten["EWT"]>("dataE"),
				vorgabenU: Storage.get<IVorgabenU>("VorgabenU"),
			});
		});
	}

	const btnSaveE = document.querySelector<HTMLButtonElement>("#btnSaveE");
	if (btnSaveE) {
		btnSaveE.addEventListener("click", () => {
			saveDaten(btnSaveE);
		});
	}

	const btnDownloadE = document.querySelector<HTMLButtonElement>("#btnDownloadE");
	if (btnDownloadE) {
		btnDownloadE.addEventListener("click", () => {
			download(btnDownloadE, "E");
		});
	}

	const btnESEE = document.querySelector<HTMLButtonElement>("#btnESEE");
	if (btnESEE) {
		btnESEE.addEventListener("click", createAddModalEWT);
	}
});

function addEventlistenerToggleBerechnen(this: CustomTable): void {
	const checkboxes = Array.from(document.querySelectorAll<HTMLInputElement>("#tableE .row-checkbox"));
	for (const checkbox of checkboxes) {
		checkbox.addEventListener("click", (event: Event) => {
			event.stopPropagation();
			const row = checkbox.closest<CustomHTMLTableRowElement>("tr")?.data;
			if (!row) return;
			const newValues = { ...row.cells, berechnen: checkbox.checked };
			row.val(newValues);
			saveTableData("tableE", this);
		});
	}
}

function createAddModalEWT(): void {
	const { modal, form } = createModal(
		"Neue Anwesenheit eingeben",
		true,
		"sm",
		createBodyElement,
		createEditorModalFooter(),
		SubmitEventListener
	);
	naechsterTag("");
	new Modal(modal).show();

	function createBodyElement(): HTMLDivElement {
		const vorgabenU: IVorgabenU = Storage.get("VorgabenU");

		const modalBody = document.createElement("div");
		modalBody.className = "modal-body row g-2";

		const Jahr = Storage.get<number>("Jahr");
		const Monat = Storage.get<number>("Monat") - 1;
		const datum = dayjs([Jahr, Monat, 1]);
		const maxDate = datum.endOf("month").format("YYYY-MM-DD");

		const naechsterTagWrapper = document.createElement("div");
		const btnNaechsterTag = document.createElement("button");
		btnNaechsterTag.className = "btn btn-secondary btn-lg text-start col-12";
		btnNaechsterTag.type = "button";
		btnNaechsterTag.id = "btnNaechsterTag";
		btnNaechsterTag.textContent = "+1 Tag";
		btnNaechsterTag.addEventListener("click", (e: MouseEvent) => {
			e.preventDefault();
			naechsterTag();
		});

		naechsterTagWrapper.appendChild(btnNaechsterTag);
		modalBody.appendChild(naechsterTagWrapper);

		modalBody.appendChild(
			createModalBodyInputElement({
				divClass: "form-floating",
				title: "Tag",
				name: "tagE",
				value: "",
				type: "date",
				required: true,
				min: datum.format("YYYY-MM-DD"),
				max: maxDate,
			})
		);

		modalBody.appendChild(
			createModalBodySelectElement({
				divClass: "form-floating",
				title: "Einsatzort",
				name: "EOrt",
				value: null,
				required: false,
				options: [
					{ value: "", text: "", selected: true },
					...vorgabenU.fZ.map(ort => {
						return {
							value: ort.key,
							text: ort.key,
						};
					}),
				],
			})
		);
		modalBody.appendChild(
			createModalBodySelectElement({
				divClass: "form-floating",
				title: "Schicht",
				name: "Schicht",
				value: "",
				options: [
					{ value: "T", text: "Tag", selected: true },
					{ value: "N", text: "Nacht" },
					{ value: "BN", text: "Nacht/Bereitschaft" },
					{ value: "S", text: "Sonder" },
				],
				required: true,
			})
		);

		modalBody.appendChild(
			createModalBodyCheckbox({
				checkClass: "form-check form-switch mt-3",
				id: "berechnen1",
				text: "Berechnen",
				status: true,
			})
		);

		return modalBody;
	}

	function SubmitEventListener(): (event: Event) => void {
		return (event: Event): void => {
			if (!(form instanceof HTMLFormElement)) return;
			if (form.checkValidity && !form.checkValidity()) return;
			event.preventDefault();

			addEWTtag();

			const table = document.querySelector<CustomHTMLTableElement>("#tableE");
			if (table) {
				saveTableData("tableE", table.instance);
			}
		};
	}
}
function createEditorModalEWT(row: CustomTable | Row, titel: string): CustomHTMLDivElement {
	const { modal, form } = createModal(
		titel,
		true,
		"fullscreen-sm-down",
		createBodyElement,
		createEditorModalFooter(customFooterButtton(), row instanceof Row ? "Speichern" : null),
		SubmitEventListener
	);

	return modal;

	function createBodyElement(): HTMLDivElement {
		const vorgabenU: IVorgabenU = Storage.get("VorgabenU");

		const modalBody = document.createElement("div");
		modalBody.className = "modal-body row";

		const Monat: number = Storage.get<number>("Monat") - 1;
		const Jahr: number = Storage.get<number>("Jahr");

		let Tag: number;
		if (row instanceof Row) {
			Tag = Number(row.cells.tagE);
		} else if (row instanceof CustomTable) {
			Tag = checkMaxTag(Jahr, Monat);
		} else throw new Error("unbekannter Fehler");

		const datum = dayjs([Jahr, Monat, Tag]);

		let column;

		column = row.columns.array.find(column => column.name === "tagE") as Column;
		modalBody.appendChild(
			createModalBodyInputElement({
				divClass: "form-floating col-12 col-sm-5 pb-3",
				title: column.title,
				name: column.name,
				value: datum.format("YYYY-MM-DD"),
				type: "date",
				required: true,
				min: datum.startOf("M").format("YYYY-MM-DD"),
				max: datum.endOf("M").format("YYYY-MM-DD"),
			})
		);

		column = row.columns.array.find(column => column.name === "eOrtE") as Column;
		modalBody.appendChild(
			createModalBodySelectElement({
				divClass: "form-floating col-12 col-sm-7 pb-3",
				title: column.title,
				name: column.name,
				value: row instanceof Row ? row.cells[column.name] : null,
				required: false,
				options: [
					{ value: "", text: "", selected: true },
					...vorgabenU.fZ.map(ort => {
						return {
							value: ort.key,
							text: ort.key,
						};
					}),
				],
			})
		);

		column = row.columns.array.find(column => column.name === "schichtE") as Column;
		modalBody.appendChild(
			createModalBodySelectElement({
				divClass: "form-floating col-12 col-sm-7 pb-3",
				title: column.title,
				name: column.name,
				value: row instanceof Row ? row.cells[column.name] : null,
				required: true,
				options: [
					{ value: "T", text: "Tag", selected: true },
					{ value: "N", text: "Nacht" },
					{ value: "BN", text: "Nacht/Bereitschaft" },
					{ value: "S", text: "Sonder" },
				],
			})
		);
		column = row.columns.array.find(column => column.name === "berechnen") as Column;
		modalBody.appendChild(
			createModalBodyCheckbox({
				checkClass: "form-check form-switch col-12 col-sm-4 pb-3",
				id: column.name,
				text: column.title,
				status: row instanceof Row ? row.cells[column.name] : true,
			})
		);

		modalBody.appendChild(document.createElement("hr"));

		const titleWrapper = document.createElement("div");
		titleWrapper.className = "icon-ewt";
		modalBody.appendChild(titleWrapper);

		titleWrapper.appendChild(createModalBodySpanElement("material-icons-round big-icons", "arrow_downward"));
		titleWrapper.appendChild(createModalBodyTitelElement("Wohnung"));
		titleWrapper.appendChild(createModalBodySpanElement("material-icons-round big-icons", "arrow_upward"));

		column = row.columns.array.find(column => column.name === "abWE") as Column;
		modalBody.appendChild(
			createModalBodyInputElement({
				divClass: "form-floating col-6 pb-3",
				title: column.title,
				name: column.name,
				value: row instanceof Row ? row.cells[column.name] : "",
				type: "time",
			})
		);

		column = row.columns.array.find(column => column.name === "anWE") as Column;
		modalBody.appendChild(
			createModalBodyInputElement({
				divClass: "form-floating col-6 pb-3",
				title: column.title,
				name: column.name,
				value: row instanceof Row ? row.cells[column.name] : "",
				type: "time",
			})
		);

		modalBody.appendChild(createModalBodyTitelElement("Arbeitszeit"));

		column = row.columns.array.find(column => column.name === "beginE") as Column;
		modalBody.appendChild(
			createModalBodyInputElement({
				divClass: "form-floating col-6 pb-3",
				title: column.title,
				name: column.name,
				value: row instanceof Row ? row.cells[column.name] : "",
				type: "time",
			})
		);

		column = row.columns.array.find(column => column.name === "endeE") as Column;
		modalBody.appendChild(
			createModalBodyInputElement({
				divClass: "form-floating col-6 pb-3",
				title: column.title,
				name: column.name,
				value: row instanceof Row ? row.cells[column.name] : "",
				type: "time",
			})
		);

		modalBody.appendChild(createModalBodyTitelElement("1. Ttigkeitssttte"));

		column = row.columns.array.find(column => column.name === "ab1E") as Column;
		modalBody.appendChild(
			createModalBodyInputElement({
				divClass: "form-floating col-6 pb-3",
				title: column.title,
				name: column.name,
				value: row instanceof Row ? row.cells[column.name] : "",
				type: "time",
			})
		);

		column = row.columns.array.find(column => column.name === "an1E") as Column;
		modalBody.appendChild(
			createModalBodyInputElement({
				divClass: "form-floating col-6 pb-3",
				title: column.title,
				name: column.name,
				value: row instanceof Row ? row.cells[column.name] : "",
				type: "time",
			})
		);

		modalBody.appendChild(createModalBodyTitelElement("Einsatzort"));

		column = row.columns.array.find(column => column.name === "anEE") as Column;
		modalBody.appendChild(
			createModalBodyInputElement({
				divClass: "form-floating col-6",
				title: column.title,
				name: column.name,
				value: row instanceof Row ? row.cells[column.name] : "",
				type: "time",
			})
		);

		column = row.columns.array.find(column => column.name === "abEE") as Column;
		modalBody.appendChild(
			createModalBodyInputElement({
				divClass: "form-floating col-6",
				title: column.title,
				name: column.name,
				value: row instanceof Row ? row.cells[column.name] : "",
				type: "time",
			})
		);

		return modalBody;
	}

	function customFooterButtton(): HTMLButtonElement[] {
		if (row instanceof Row) {
			const btnClearZeiten = document.createElement("button");
			btnClearZeiten.type = "button";
			btnClearZeiten.className = "btn btn-danger";
			btnClearZeiten.ariaLabel = "Zeiten lschen";
			btnClearZeiten.innerHTML = "Zeiten lschen";
			btnClearZeiten.addEventListener("click", () => clearZeiten(modal));
			return [btnClearZeiten];
		}
		return [];
	}

	function SubmitEventListener(): (event: Event) => void {
		return (event: Event) => {
			if (!(form instanceof HTMLFormElement)) return;
			event.preventDefault();
			if (!form.checkValidity()) return;

			const row = modal.row;
			if (!row) throw new Error("Row nicht gefunden");
			const table = row instanceof Row ? row.CustomTable : row;

			const values: IDaten["EWT"][0] = {
				tagE: dayjs(form.querySelector<HTMLInputElement>("#tagE")?.value).format("DD") ?? "",
				eOrtE: form.querySelector<HTMLInputElement>("#eOrtE")?.value ?? "",
				schichtE: form.querySelector<HTMLInputElement>("#schichtE")?.value ?? "",
				abWE: form.querySelector<HTMLInputElement>("#abWE")?.value ?? "",
				ab1E: form.querySelector<HTMLInputElement>("#ab1E")?.value ?? "",
				anEE: form.querySelector<HTMLInputElement>("#anEE")?.value ?? "",
				beginE: form.querySelector<HTMLInputElement>("#beginE")?.value ?? "",
				endeE: form.querySelector<HTMLInputElement>("#endeE")?.value ?? "",
				abEE: form.querySelector<HTMLInputElement>("#abEE")?.value ?? "",
				an1E: form.querySelector<HTMLInputElement>("#an1E")?.value ?? "",
				anWE: form.querySelector<HTMLInputElement>("#anWE")?.value ?? "",
				berechnen: form.querySelector<HTMLInputElement>("#berechnen")?.checked ?? true,
			};

			row instanceof Row ? row.val(values) : row.rows.add(values);

			(<Modal>Modal.getInstance(modal)).hide();
			saveTableData("tableE", table);
		};
	}
}
function createShowModalEWT(row: Row, title: string): CustomHTMLDivElement {
	const { modal } = createModal(title, false, "sm", createModalBody, createShowModalFooter);
	return modal;

	function createModalBody(): HTMLDivElement {
		const modalBody = document.createElement("div");
		modalBody.className = "modal-body row";
		let column;

		column = row.columns.array.find(column => column.name === "tagE") as Column;
		modalBody.appendChild(
			createModalBodyShowElement({
				divClass: "mb-1 col-6",
				labelClass: "col-5 col-form-label text-wrap fw-bold",
				spanClass: "col-2 align-middle text-break my-auto",
				title: `${column.title}:`,
				name: column.name,
				text: column.parser(row.cells[column.name]),
			})
		);

		column = row.columns.array.find(column => column.name === "berechnen") as Column;
		const berechnenCheckbox = createModalBodyCheckbox({
			checkClass: "form-check form-switch col-5",
			id: column.name,
			text: column.title,
			status: (row.cells && row.cells[column.name]) ?? true,
		});
		berechnenCheckbox.addEventListener("click", (e: MouseEvent) => {
			const row = (<CustomHTMLDivElement>(<HTMLInputElement>e.target).closest(".modal")).row as Row;
			row.cells.berechnen = (<HTMLInputElement>e.target).checked;
			const table = row.CustomTable;
			table.drawRows();
			saveTableData("tableE", table);
		});
		modalBody.appendChild(berechnenCheckbox);

		column = row.columns.array.find(column => column.name === "eOrtE") as Column;
		modalBody.appendChild(
			createModalBodyShowElement({
				labelClass: "col-4 col-sm-5 col-form-label text-wrap fw-bold",
				spanClass: "col-8 col-sm-7 align-middle text-break my-auto",
				title: `${column.title}:`,
				name: column.name,
				text: column.parser(row.cells[column.name]),
			})
		);

		column = row.columns.array.find(column => column.name === "schichtE") as Column;
		modalBody.appendChild(
			createModalBodyShowElement({
				labelClass: "col-4 col-sm-5 col-form-label text-wrap fw-bold",
				spanClass: "col-8 col-sm-7 align-middle text-break my-auto",
				title: `${column.title}:`,
				name: column.name,
				text: column.parser(row.cells[column.name]),
			})
		);

		modalBody.appendChild(document.createElement("hr"));

		const titleWrapper = document.createElement("div");
		titleWrapper.className = "icon-ewt-arrow";
		modalBody.appendChild(titleWrapper);

		titleWrapper.appendChild(createModalBodySpanElement("material-icons-round big-icons", "arrow_downward"));
		titleWrapper.appendChild(createModalBodySpanElement("material-icons-round big-icons", "arrow_upward"));

		modalBody.appendChild(
			createModalTitleElement({
				vorTitle: "ab",
				title: "Wohnung",
				nachTitle: "an",
			})
		);

		column = row.columns.array.find(column => column.name === "abWE") as Column;
		modalBody.appendChild(
			createModalBodyShowElement2({
				name: column.name,
				text: column.parser(row.cells[column.name]).toString(),
			})
		);
		column = row.columns.array.find(column => column.name === "anWE") as Column;
		modalBody.appendChild(
			createModalBodyShowElement2({
				name: column.name,
				text: column.parser(row.cells[column.name]).toString(),
			})
		);

		modalBody.appendChild(
			createModalTitleElement({
				vorTitle: "von",
				title: "Arbeitszeit",
				nachTitle: "bis",
			})
		);

		column = row.columns.array.find(column => column.name === "beginE") as Column;
		modalBody.appendChild(
			createModalBodyShowElement2({
				name: column.name,
				text: column.parser(row.cells[column.name]).toString(),
			})
		);
		column = row.columns.array.find(column => column.name === "endeE") as Column;
		modalBody.appendChild(
			createModalBodyShowElement2({
				name: column.name,
				text: column.parser(row.cells[column.name]).toString(),
			})
		);

		modalBody.appendChild(
			createModalTitleElement({
				vorTitle: "ab",
				title: "1. Ttigkeitssttte",
				nachTitle: "an",
			})
		);

		column = row.columns.array.find(column => column.name === "ab1E") as Column;
		modalBody.appendChild(
			createModalBodyShowElement2({
				name: column.name,
				text: column.parser(row.cells[column.name]).toString(),
			})
		);
		column = row.columns.array.find(column => column.name === "an1E") as Column;
		modalBody.appendChild(
			createModalBodyShowElement2({
				name: column.name,
				text: column.parser(row.cells[column.name]).toString(),
			})
		);

		modalBody.appendChild(
			createModalTitleElement({
				vorTitle: "an",
				title: "Einsatzort",
				nachTitle: "ab",
			})
		);

		column = row.columns.array.find(column => column.name === "anEE") as Column;
		modalBody.appendChild(
			createModalBodyShowElement2({
				name: column.name,
				text: column.parser(row.cells[column.name]).toString(),
			})
		);
		column = row.columns.array.find(column => column.name === "abEE") as Column;
		modalBody.appendChild(
			createModalBodyShowElement2({
				name: column.name,
				text: column.parser(row.cells[column.name]).toString(),
			})
		);

		return modalBody;

		function createModalTitleElement(options: { vorTitle: string; title: string; nachTitle: string; divClass?: string }) {
			const titleWrapper = document.createElement("div");
			titleWrapper.className = options.divClass ?? "icon-ewt";

			const title = document.createElement("h5");
			title.className = "my-1";
			title.textContent = options.title;

			titleWrapper.appendChild(createModalBodySpanElement("", options.vorTitle));
			titleWrapper.appendChild(title);
			titleWrapper.appendChild(createModalBodySpanElement("", options.nachTitle));

			return titleWrapper;
		}

		function createModalBodyShowElement2(options: { name: string; text: string; divClass?: string; spanClass?: string }) {
			const divElement = document.createElement("div");
			divElement.className = options.divClass ?? "mb-1 col-6 text-center";

			const span = document.createElement("span");
			span.className = options.spanClass ?? "";
			span.id = options.name;
			span.innerHTML = options.text; //|| "--:--";
			divElement.appendChild(span);

			return divElement;
		}
	}
}

export function DataE(data?: IDaten["EWT"]): IDaten["EWT"] {
	if (data === undefined) {
		if (!Storage.check("dataE")) return [];
		data = Storage.get<IDaten["EWT"]>("dataE");
	}

	if (data === undefined) return [];
	return data;
}

function clearZeiten(modal: CustomHTMLDivElement): void {
	const inputIds = ["abWE", "ab1E", "anEE", "beginE", "endeE", "abEE", "an1E", "anWE"];
	inputIds.forEach(id => {
		const input = modal.querySelector<HTMLInputElement>(`#${id}`);
		if (input) {
			input.value = "";
		}
	});
}

function ewtBerechnen({
	monat,
	jahr,
	daten,
	vorgabenU,
}: {
	monat: number;
	jahr: number;
	daten: IDaten["EWT"];
	vorgabenU: IVorgabenU;
}) {
	const convertedDaten = convertDaten(daten, jahr, monat);

	DatenSortieren(convertedDaten);

	const berechneteDaten = berechnen(vorgabenU, convertedDaten, jahr, monat);

	for (const zeile of berechneteDaten) {
		if (!zeile.berechnen) continue;
		zeile.tagE = zeile.tagE.toString().padStart(2, "0");
		for (const [key, value] of Object.entries(zeile)) {
			switch (key) {
				case "tagE":
				case "schichtE":
				case "eOrtE":
				case "berechnen":
					break;
				case "abWE":
				case "ab1E":
				case "anEE":
				case "beginE":
				case "endeE":
				case "abEE":
				case "an1E":
				case "anWE":
					if (value) zeile[key] = (value as dayjs.Dayjs).format("LT");
					break;
				default:
					throw new Error("Datenfehler");
			}
		}
	}

	const table = document.querySelector<CustomHTMLTableElement>("#tableE");
	if (!table) throw new Error("Tabelle nicht gefunden");
	const ftE = table.instance;
	console.log("save ", { ftE });
	ftE.rows.load(DataE(convertedDaten));

	saveTableData("tableE", ftE);
	createSnackBar({
		message: `EWT<br/>Zeiten berechnet.`,
		status: "success",
		timeout: 3000,
		position: "br",
		fixed: true,
	});

	function convertDaten(daten: IDaten["EWT"], jahr: number, monat: number): IDaten["EWT"] {
		for (const zeile of daten) {
			if (!zeile.berechnen) continue;
			const tag = Number(zeile.tagE);
			zeile.tagE = tag.toString();

			for (const [key, value] of Object.entries(zeile)) {
				switch (key) {
					case "tagE":
					case "schichtE":
					case "eOrtE":
					case "berechnen":
						break;
					case "abWE":
					case "ab1E":
					case "anEE":
					case "beginE":
					case "endeE":
					case "abEE":
					case "an1E":
					case "anWE":
						if (value) {
							let tagI = tag;
							const zeit = (<string>value).split(":");
							if (["endeE", "abEE", "an1E", "anWE"].includes(key) && ["BN", "N"].includes(zeile.schichtE)) {
								tagI = +zeile.tagE - 1;
							}
							zeile[key] = dayjs([jahr, monat - 1, tagI, +zeit[0], +zeit[1], 0, 0]);
						} else {
							zeile[key] = "";
						}
						break;
					default:
						throw new Error("Datenfehler");
				}
			}
		}
		return daten;
	}
}

function berechnen(vorgabenU: IVorgabenU, daten: IDaten["EWT"], jahr: number, monat: number) {
	let beginn, ende, svzA, svzE, datumAb1, datumBeginn, datumEnde, datumAn1;

	const endePascal =
		vorgabenU.pers.Vorname === "Ackermann" && vorgabenU.pers.Nachname === "Pascal"
			? dayjs.duration(5, "m")
			: dayjs.duration(0, "m");

	const getTimeForDuration = (value: dayjs.ConfigType) => {
		const time = dayjs(value, "HH:mm");
		return { hours: time.hour(), minutes: time.minute() };
	};

	const vorgabenE = { fZ: {} } as IVorgabenE;

	Object.entries(vorgabenU.aZ).forEach(([key, value]) => {
		vorgabenE[key] = dayjs.duration(getTimeForDuration(value));
	});

	vorgabenU.fZ.forEach(ort => {
		vorgabenE.fZ[ort.key] = dayjs.duration(getTimeForDuration(ort.value));
	});

	// Beginn der Berechnung
	for (const Tag of daten) {
		if (!Tag.berechnen) continue;
		const datum = dayjs([jahr, monat - 1, +Tag.tagE]);
		switch (Tag.schichtE) {
			case "N":
				beginn = vorgabenE.bN;
				ende = vorgabenE.eN.add(1, "d");
				svzA = dayjs.duration(45, "m");
				svzE = svzA.clone();
				break;
			case "S":
				beginn = vorgabenE.bS;
				ende = vorgabenE.eS;
				svzA = dayjs.duration(20, "m");
				svzE = svzA.clone();
				break;
			case "BN":
				beginn = vorgabenE.bBN;
				ende = vorgabenE.eN.add(1, "d");
				svzA = dayjs.duration(60, "m");
				svzE = dayjs.duration(45, "m");
				break;
			case "T":
				beginn = vorgabenE.bT;
				ende = datum.isoWeekday() === 5 ? vorgabenE.eTF : vorgabenE.eT;
				svzA = dayjs.duration(20, "m");
				svzE = svzA.clone();
				break;
			default:
				throw new Error("Schichtfehler");
		}

		// Arbeitszeit Beginn
		Tag.beginE = datumBeginn = Tag.beginE === "" ? datum.add(beginn) : Tag.beginE;
		// Arbeitszeit Ende
		Tag.endeE = datumEnde = Tag.endeE === "" ? datum.add(ende) : Tag.endeE;
		// Ab Wohnung
		Tag.abWE = Tag.abWE === "" ? (<dayjs.Dayjs>datumBeginn).subtract(vorgabenE.rZ) : Tag.abWE;
		// Ab erste Ttigkeitssttte
		Tag.ab1E = datumAb1 = Tag.ab1E === "" ? (<dayjs.Dayjs>datumBeginn).add(svzA) : Tag.ab1E;
		// An erste Ttigkeitssttte
		Tag.an1E = datumAn1 = Tag.an1E === "" ? (<dayjs.Dayjs>datumEnde).subtract(svzE) : Tag.an1E;
		// An Wohnung
		Tag.anWE = Tag.anWE === "" ? (<dayjs.Dayjs>datumEnde).add(vorgabenE.rZ).add(endePascal) : Tag.anWE;
		if (Tag.eOrtE !== "") {
			// An Einsatzort
			Tag.anEE = Tag.anEE === "" ? (<dayjs.Dayjs>datumAb1).add(vorgabenE.fZ[Tag.eOrtE]) : Tag.anEE;
			// Ab Einsatzort
			Tag.abEE = Tag.abEE === "" ? (<dayjs.Dayjs>datumAn1).subtract(vorgabenE.fZ[Tag.eOrtE]) : Tag.abEE;
		}
	}
	return daten;
}

function naechsterTag(
	tag?: string | number | null,
	dataE: IDaten["EWT"] | { [key: string]: unknown }[] | [] = tableToArray("tableE")
): void {
	const eingabefeldTagE = document.querySelector<HTMLInputElement>("#tagE");
	if (!eingabefeldTagE) throw new Error("Eingabefeld fr Tag nicht gefunden");

	const vorhandeTage = new Set(dataE.map(tag => +(tag.tagE as string))),
		letzterTag = dayjs(eingabefeldTagE.max).date();
	let loop = false;
	if (tag == null) tag = dayjs(eingabefeldTagE.value).date();
	if (tag == "") tag = Math.max(...vorhandeTage.values()) | 0;
	if (typeof tag === "string") tag = +tag;

	vorhandeTage.delete(0);

	do {
		tag++;
		if (tag > letzterTag && !loop) {
			tag = 1;
			loop = true;
		} else if (tag > letzterTag && loop) {
			document
				.querySelector<HTMLButtonElement>("#modal > div > form > div.modal-footer > button.btn.btn-primary")
				?.setAttribute("disabled", "true");
			createSnackBar({
				message: `EWT<br/>Fehler beim Finden eines Freien Tages.`,
				status: "error",
				timeout: 3000,
				position: "br",
				fixed: true,
			});
			throw new Error("Fehler beim Finden eines Freien Tages");
		}
	} while (vorhandeTage.has(tag));

	eingabefeldTagE.value = dayjs([Storage.get<number>("Jahr"), Storage.get<number>("Monat") - 1, tag]).format(
		"YYYY-MM-DD"
	);
}

function addEWTtag(): void {
	const tagEInput = document.querySelector<HTMLInputElement>("#tagE");
	if (tagEInput === null) throw new Error("TagE input not found");
	const tagE = `0${tagEInput.value}`.slice(-2);

	const eOrtESelect = document.querySelector<HTMLSelectElement>("#EOrt");
	if (eOrtESelect === null) throw new Error("EOrt select not found");
	const eOrtE = eOrtESelect.value;

	const schichtESelect = document.querySelector<HTMLSelectElement>("#Schicht");
	if (schichtESelect === null) throw new Error("Schicht select not found");
	const schichtE = schichtESelect.value;

	const berechnenInput = document.querySelector<HTMLInputElement>("#berechnen1");
	if (berechnenInput === null) throw new Error("Berechnen input not found");
	const berechnen = berechnenInput.checked;

	const data: IDaten["EWT"][0] = {
		tagE,
		eOrtE,
		schichtE,
		abWE: "",
		ab1E: "",
		anEE: "",
		beginE: "",
		endeE: "",
		abEE: "",
		an1E: "",
		anWE: "",
		berechnen,
	};

	const tableE = document.querySelector<CustomHTMLTableElement>("#tableE");
	if (tableE === null) throw new Error("TableE not found");

	const ftE = tableE.instance;
	console.log("save ", { ftE });
	ftE.rows.add(data);

	saveTableData("tableE", ftE);

	const existingRows = ftE.getRows().map(row => row.cells);
	naechsterTag(+tagE, existingRows);
}
