import dayjs from "dayjs";
import arraySupport from "dayjs/plugin/arraySupport.js";
import { generateTableBerechnung } from "./Berechnung";
import { DataBE, DataBZ } from "./Bereitschaft";
import {
	buttonDisable,
	clearLoading,
	controller,
	decodeAccessToken,
	FetchRetry,
	newAbortControler,
	setDisableButton,
	setLoading,
	Storage,
} from "./Core";
import { generateEingabeMaskeEinstellungen } from "./Einstellungen";
import { DataE } from "./EWT";
import { CustomHTMLTableElement, IDaten, IVorgabenBerechnung, IVorgabenGeld, IVorgabenU } from "./Interfaces";
import { createSnackBar } from "./CustomSnackbar";
import { DataN } from "./Neben";
dayjs.extend(arraySupport);

window.addEventListener("load", () => {
	if (Storage.length() > 3 && Storage.check("UserID")) {
		const benutzer = Storage.get<string>("Benutzer");
		Logout();
		createSnackBar({
			message: `Hallo ${benutzer},<br/>die App hat ein Update erhalten.<br/>Bitte melde dich neu an, um<br/>die neuen Funktionen zu nutzen.`,
			status: "info",
			timeout: 10000,
			position: "br",
			fixed: true,
		});
	}
	const formLogin = document.querySelector<HTMLFormElement>("#formLogin");
	if (formLogin) {
		formLogin.addEventListener("submit", e => {
			e.preventDefault();
			loginUser();
		});
	}

	const formNewUser = document.querySelector<HTMLFormElement>("#formNewUser");
	if (formNewUser) {
		formNewUser.addEventListener("submit", e => {
			e.preventDefault();
			checkNeuerBenutzer();
		});
	}

	const formChangePW = document.querySelector<HTMLFormElement>("#formChangePW");
	if (formChangePW) {
		formChangePW.addEventListener("submit", e => {
			e.preventDefault();
			checkPasswort();
		});
	}

	const formSelectMonatJahr = document.querySelector<HTMLFormElement>("#formSelectMonatJahr");
	if (formSelectMonatJahr) {
		formSelectMonatJahr.addEventListener("submit", e => {
			e.preventDefault();
			SelectYear();
		});
	}

	const btnNewUser = document.querySelector<HTMLButtonElement>("#btnNewUser");
	if (btnNewUser) {
		btnNewUser.addEventListener("click", () => {
			const loginDisplay = document.querySelector<HTMLDivElement>("#loginDisplay");
			const newDisplay = document.querySelector<HTMLDivElement>("#NewDisplay");
			if (loginDisplay && newDisplay) {
				loginDisplay.classList.add("d-none");
				newDisplay.classList.remove("d-none");
			}
		});
	}

	const btnCancelNewUser = document.querySelector<HTMLButtonElement>("#btnCancelNewUser");
	if (btnCancelNewUser) {
		btnCancelNewUser.addEventListener("click", () => {
			const newDisplay = document.querySelector<HTMLDivElement>("#NewDisplay");
			const loginDisplay = document.querySelector<HTMLDivElement>("#loginDisplay");
			if (newDisplay && loginDisplay) {
				newDisplay.classList.add("d-none");
				loginDisplay.classList.remove("d-none");
			}
		});
	}

	const btnCancelChangePW = document.querySelector<HTMLButtonElement>("#btnCancelChangePW");
	if (btnCancelChangePW) {
		btnCancelChangePW.addEventListener("click", () => {
			const changeDisplay = document.querySelector<HTMLDivElement>("#ChangeDisplay");
			const selectDisplay = document.querySelector<HTMLDivElement>("#SelectDisplay");
			if (changeDisplay && selectDisplay) {
				changeDisplay.classList.add("d-none");
				selectDisplay.classList.remove("d-none");
			}
		});
	}

	const btnPasswortAEndern = document.querySelector<HTMLButtonElement>("#btnPasswortAEndern");
	if (btnPasswortAEndern) {
		btnPasswortAEndern.addEventListener("click", () => {
			const selectDisplay = document.querySelector<HTMLDivElement>("#SelectDisplay");
			const changeDisplay = document.querySelector<HTMLDivElement>("#ChangeDisplay");
			if (selectDisplay && changeDisplay) {
				selectDisplay.classList.add("d-none");
				changeDisplay.classList.remove("d-none");
			}
		});
	}

	const btnLogout = document.querySelector<HTMLButtonElement>("#btnLogout");
	if (btnLogout) {
		btnLogout.addEventListener("click", Logout);
	}

	const Monat = document.querySelector<HTMLInputElement>("#Monat");
	if (Monat) {
		Monat.addEventListener("change", changeMonatJahr);
	}

	const Jahr = document.querySelector<HTMLInputElement>("#Jahr");
	if (Jahr) {
		Jahr.addEventListener("change", changeMonatJahr);
	}

	const willkommenEl = document.querySelector<HTMLHeadingElement>("#Willkommen");
	const jahrEl = document.querySelector<HTMLInputElement>("#Jahr");
	const monatEl = document.querySelector<HTMLInputElement>("#Monat");
	const loginDisplayEl = document.querySelector<HTMLDivElement>("#loginDisplay");
	const selectDisplayEl = document.querySelector<HTMLDivElement>("#SelectDisplay");

	const nebenTab = document.querySelector<HTMLButtonElement>("#Neben-tab");
	if (!nebenTab) throw new Error("Element nicht gefunden");
	const nebenTabEl = nebenTab.parentElement as HTMLLIElement;

	const adminEl = document.querySelector<HTMLDivElement>("#admin");
	const navmenuEl = document.querySelector<HTMLDivElement>("#navmenu");
	const btnNavmenuEl = document.querySelector<HTMLButtonElement>("#btn-navmenu");

	if (Storage.check("Benutzer") && Storage.check("accessToken")) {
		const benutzer = Storage.check("VorgabenU")
			? Storage.get<IVorgabenU>("VorgabenU").pers.Vorname
			: Storage.get<string>("Benutzer");
		if (willkommenEl) willkommenEl.innerHTML = `Hallo, ${benutzer}.`;
		if (loginDisplayEl) loginDisplayEl.classList.add("d-none");

		const jahr = Storage.get<number>("Jahr");
		if (jahrEl) jahrEl.value = jahr.toString();

		const monat = Storage.get<number>("Monat");
		if (monatEl) monatEl.value = monat.toString();

		if (selectDisplayEl) selectDisplayEl.classList.remove("d-none");
		console.log("Benutzer gefunden");

		const vorgabenU = Storage.get<IVorgabenU>("VorgabenU");
		if (vorgabenU && vorgabenU.pers.TB == "Tarifkraft" && nebenTabEl) {
			nebenTabEl.classList.remove("d-none");
		}

		if (Storage.check("accessToken")) {
			const berechtigung = decodeAccessToken().Berechtigung;
			if (adminEl && berechtigung & 2) {
				adminEl.classList.remove("d-none");
			}
		}

		if (navmenuEl) navmenuEl.classList.remove("d-none");
		if (btnNavmenuEl) btnNavmenuEl.classList.remove("d-none");

		if (navigator.onLine) SelectYear(monat, jahr);
	}
});

export function changeMonatJahr(): void {
	const monatInput = document.querySelector<HTMLInputElement>("#Monat");
	const jahrInput = document.querySelector<HTMLInputElement>("#Jahr");
	if (
		monatInput &&
		jahrInput &&
		monatInput.value === Storage.get<number>("Monat").toString() &&
		jahrInput.value === Storage.get<number>("Jahr").toString()
	) {
		buttonDisable(false);
		if (!navigator.onLine) setDisableButton(true);
		return;
	}
	buttonDisable(true);
	const auswaehlenBtn = document.querySelector<HTMLButtonElement>("#btnAuswaehlen");
	if (navigator.onLine && auswaehlenBtn) auswaehlenBtn.disabled = false;
}

async function loginUser(username?: string, passwort?: string): Promise<void> {
	const usernameInput = document.querySelector<HTMLInputElement>("#Benutzer");
	if (!usernameInput) throw new Error("Benutzer Input nicht gefunden");
	if (!username) {
		username = usernameInput.value;
	}
	const passwortInput = document.querySelector<HTMLInputElement>("#Passwort");
	if (!passwortInput) throw new Error("Passwort Input nicht gefunden");
	if (!passwort) {
		passwort = passwortInput.value;
	}
	const btnLogin = document.querySelector<HTMLButtonElement>("#btnLogin");
	if (btnLogin) {
		btnLogin.disabled = true;
	}
	setLoading("btnLogin");

	const errorMessage = document.querySelector<HTMLDivElement>("#errorMessage");
	if (!errorMessage) throw new Error("Error Nachrichtenfeld nicht gefunden");

	const data = { Name: username, Passwort: passwort };

	try {
		const fetched = await FetchRetry<{ Name: string; Passwort: string }, { accessToken: string; refreshToken: string }>(
			"login",
			data,
			"POST"
		);
		if (fetched.statusCode == 200) {
			const { accessToken, refreshToken } = fetched.data;
			console.log({ accessToken, refreshToken });
			username = `${username[0].toUpperCase()}${username.substring(1)}`;
			Storage.set("Benutzer", username);
			const willkommen = document.querySelector<HTMLHeadingElement>("#Willkommen");
			if (willkommen) {
				willkommen.innerHTML = `Hallo, ${username}.`;
			}
			document.querySelector<HTMLDivElement>("#loginDisplay")?.classList.add("d-none");
			document.querySelector<HTMLDivElement>("#ChangeDisplay")?.classList.add("d-none");
			document.querySelector<HTMLDivElement>("#NewDisplay")?.classList.add("d-none");

			Storage.set("accessToken", accessToken);
			Storage.set("refreshToken", refreshToken);

			const aktJahr = dayjs().year();
			const jahrInput = document.querySelector<HTMLInputElement>("#Jahr");
			if (jahrInput) {
				jahrInput.value = aktJahr.toString();
			}

			const monat = dayjs().month() + 1;
			const monatInput = document.querySelector<HTMLInputElement>("#Monat");
			if (monatInput) {
				monatInput.value = monat.toString();
			}

			document.querySelector<HTMLDivElement>("#SelectDisplay")?.classList.remove("d-none");
			usernameInput.value = "";
			passwortInput.value = "";

			errorMessage.innerHTML = "";

			if (Storage.check("accessToken")) {
				if (decodeAccessToken().Berechtigung & 2)
					document.querySelector<HTMLDivElement>("#admin")?.classList.remove("d-none");
			}

			console.log("Eingeloggt");
			SelectYear(monat, aktJahr);
		} else {
			errorMessage.innerHTML = fetched.message;
		}
	} catch (err) {
		err instanceof Error ? console.log(err.message) : console.log(err);
	} finally {
		clearLoading("btnLogin");
	}
}

function SelectYear(monat?: number, jahr?: number): void {
	const monatInput = document.querySelector<HTMLInputElement>("#Monat");
	if (!monatInput) throw new Error("Monats Input nicht gefunden");
	if (!monat) monat = +monatInput.value;

	const jahrInput = document.querySelector<HTMLInputElement>("#Jahr");
	if (!jahrInput) throw new Error("Jahres Input nicht gefunden");
	if (!jahr) jahr = +jahrInput.value;

	const errorMessage = document.querySelector<HTMLDivElement>("#errorMessage");
	if (!errorMessage) throw new Error("Error Nachrichtenfeld nicht gefunden");

	setLoading("btnAuswaehlen");
	errorMessage.innerHTML = "";

	if (Storage.check("Jahr") && Storage.check("Monat")) {
		if (Storage.get<number>("Jahr") != jahr || Storage.get<number>("Monat") != monat) {
			Storage.set("monatswechsel", true);
		}
	}
	Storage.set("Jahr", jahr);
	Storage.set("Monat", monat);

	setMonatJahr(jahr, monat);

	if (!Storage.check("accessToken")) return;

	LadeUserDaten(monat, jahr);
}

export function Logout(): void {
	controller.abort();
	newAbortControler();
	Storage.clear();

	const navmenu = document.querySelector<HTMLDivElement>("#navmenu");
	if (navmenu) {
		navmenu.classList.add("d-none");
	}

	const btnNavmenu = document.querySelector<HTMLButtonElement>("#btn-navmenu");
	if (btnNavmenu) {
		btnNavmenu.classList.add("d-none");
	}

	const loginDisplay = document.querySelector<HTMLDivElement>("#loginDisplay");
	if (loginDisplay) {
		loginDisplay.classList.remove("d-none");
	}

	const newDisplay = document.querySelector<HTMLDivElement>("#NewDisplay");
	if (newDisplay) {
		newDisplay.classList.add("d-none");
	}

	const selectDisplay = document.querySelector<HTMLDivElement>("#SelectDisplay");
	if (selectDisplay) {
		selectDisplay.classList.add("d-none");
	}

	const admin = document.querySelector<HTMLDivElement>("#admin");
	if (admin) {
		admin.classList.add("d-none");
	}

	const tab = document.querySelector<HTMLLIElement>("#Neben-tab");
	const tabParent = tab?.parentElement;
	if (tab && tabParent) {
		tabParent.classList.add("d-none");
	}

	const willkommen = document.querySelector<HTMLHeadingElement>("#Willkommen");
	if (willkommen) {
		willkommen.innerHTML = "Willkommen";
	}
}

async function checkPasswort(): Promise<void> {
	const errorMessage = document.querySelector<HTMLDivElement>("#errorMessage");
	if (!errorMessage) {
		throw new Error("Fehler: errorMessage nicht gefunden!");
	}
	const passwortAltInput = document.querySelector<HTMLInputElement>("#passwortOld");
	if (!passwortAltInput) {
		throw new Error("Fehler: passwortOld InputElement nicht gefunden!");
	}
	const passwortAlt = passwortAltInput.value.trim();

	const passwort3Input = document.querySelector<HTMLInputElement>("#Passwort3");
	if (!passwort3Input) {
		throw new Error("Fehler: Passwort3 InputElement nicht gefunden!");
	}
	const passwort3 = passwort3Input.value.trim();

	const passwort4Input = document.querySelector<HTMLInputElement>("#Passwort4");
	if (!passwort4Input) {
		throw new Error("Fehler: Passwort4 InputElement nicht gefunden!");
	}
	const passwort4 = passwort4Input.value.trim();

	if (!passwortAlt) {
		errorMessage.textContent = "Bitte Aktuelles Passwort Eingeben";
		return;
	}
	if (!passwort3) {
		errorMessage.textContent = "Bitte Neues Passwort Eingeben";
		return;
	}
	if (!passwort4) {
		errorMessage.textContent = "Bitte Neues Passwort wiederholen";
		return;
	}
	if (passwort3 != passwort4) {
		errorMessage.textContent = "Passworter falsch wiederholt";
		return;
	}
	if (passwortAlt == passwort3) {
		errorMessage.textContent = "Passwrter Alt und Neu sind gleich";
		return;
	}

	setLoading("btnChange");

	try {
		const data = {
			PasswortAlt: passwortAlt,
			PasswortNeu: passwort3,
		};

		const fetched = await FetchRetry("changePW", data, "POST");
		if (fetched.statusCode >= 400) {
			console.log(fetched.message);
			errorMessage.innerHTML = fetched.message;
			createSnackBar({
				message: `Login<br/>Passwort konnte nicht gendert werden.`,
				status: "error",
				timeout: 3000,
				position: "br",
				fixed: true,
			});
			return;
		} else if (fetched.statusCode == 200) {
			console.log(`Passwort gendert: ${fetched.data}`);
			createSnackBar({
				message: `Login<br/>Passwort wurde gendert.`,
				status: "success",
				timeout: 3000,
				position: "br",
				fixed: true,
			});
			errorMessage.innerHTML = "";
		} else {
			console.log("Fehler");
			createSnackBar({
				message: `Login<br/>Passwort konnte nicht gendert werden.`,
				status: "error",
				timeout: 3000,
				position: "br",
				fixed: true,
			});
			return;
		}
		const selectDisplay = document.querySelector<HTMLDivElement>("#SelectDisplay");
		if (!selectDisplay) {
			throw new Error("Fehler: SelectDisplay nicht gefunden!");
		}
		selectDisplay.classList.remove("d-none");

		const changeDisplay = document.querySelector<HTMLDivElement>("#ChangeDisplay");
		if (!changeDisplay) {
			throw new Error("Fehler: ChangeDisplay nicht gefunden!");
		}
		changeDisplay.classList.add("d-none");

		passwortAltInput.value = "";
		passwort3Input.value = "";
		passwort4Input.value = "";
	} catch (err) {
		console.log(err);
		return;
	} finally {
		clearLoading("btnChange");
	}
}

async function checkNeuerBenutzer(): Promise<void> {
	const errorMessage = document.querySelector<HTMLDivElement>("#errorMessage");
	if (!errorMessage) {
		throw new Error("errorMessage not found");
	}
	const zugangscode = document.querySelector<HTMLInputElement>("#Zugang");
	if (!zugangscode || !zugangscode.value.trim()) {
		errorMessage.textContent = "Bitte Zugangscode Eingeben";
		return;
	}
	const benutzer = document.querySelector<HTMLInputElement>("#Benutzer2");
	if (!benutzer || !benutzer.value.trim()) {
		errorMessage.textContent = "Bitte Benutzername Eingeben";
		return;
	}
	const passwort1 = document.querySelector<HTMLInputElement>("#Passwort1");
	if (!passwort1 || !passwort1.value.trim()) {
		errorMessage.textContent = "Bitte Passwort Eingeben";
		return;
	}
	const passwort2 = document.querySelector<HTMLInputElement>("#Passwort2");
	if (!passwort2 || !passwort2.value.trim()) {
		errorMessage.textContent = "Bitte Passwort wiederholen";
		return;
	}
	if (passwort1.value !== passwort2.value) {
		errorMessage.textContent = "Passwrter falsch wiederholt";
		return;
	}
	setLoading("btnNeu");
	const data = {
		Code: zugangscode.value.trim(),
		Name: benutzer.value.trim(),
		Passwort: passwort1.value.trim(),
	};
	try {
		const fetched = await FetchRetry("add", data, "POST");
		if (fetched.statusCode >= 400) {
			console.log(fetched.message);
			errorMessage.innerHTML = fetched.message;
			return;
		}
		if (fetched.statusCode == 201) {
			createSnackBar({
				message: `Registrierung<br/>Benutzer erfolgreich angelegt.`,
				status: "success",
				timeout: 3000,
				position: "br",
				fixed: true,
			});
		} else {
			console.log("Fehler: ", fetched.message);
			createSnackBar({
				message: `Registrierung <br/>Fehler bei Benutzerstellung: </br>${fetched.message}`,
				status: "error",
				timeout: 3000,
				position: "br",
				fixed: true,
			});
			return;
		}
	} catch (err) {
		console.log(err);
		return;
	} finally {
		clearLoading("btnNeu");
	}

	errorMessage.innerHTML = "";

	loginUser(benutzer.value, passwort1.value);

	zugangscode.value = "";
	benutzer.value = "";
	passwort1.value = "";
	passwort2.value = "";
}

async function LadeUserDaten(monat: number, jahr: number): Promise<void> {
	setMonatJahr(jahr, monat);
	let user: {
		vorgabenU: IVorgabenU;
		datenGeld: IVorgabenGeld;
		datenBerechnung: IVorgabenBerechnung;
		datenB: { datenBZ: IDaten["BZ"]; datenBE: IDaten["BE"] };
		datenE: IDaten["EWT"];
		datenN: IDaten["N"];
	};
	try {
		const fetched = await FetchRetry<
			null,
			{
				vorgabenU: IVorgabenU;
				datenBerechnung: IVorgabenBerechnung;
				datenGeld: IVorgabenGeld;
				datenB: { datenBZ: IDaten["BZ"]; datenBE: IDaten["BE"] };
				datenE: IDaten["EWT"];
				datenN: IDaten["N"];
			}
		>(`${monat}&${jahr}`);
		if (fetched.statusCode == 200) {
			user = fetched.data;
		} else {
			throw new Error(fetched.message);
		}
	} catch (err) {
		console.error(err);
		createSnackBar({
			message: `Server <br/>Keine Verbindung zum Server oder Serverfehler.`,
			status: "error",
			timeout: 3000,
			position: "br",
			fixed: true,
		});
		return;
	} finally {
		clearLoading("btnAuswaehlen");
	}

	const dataArray = user;

	console.log("Daten geladen: ", dataArray);
	const { datenBerechnung, datenGeld } = dataArray;
	let { vorgabenU } = dataArray;
	let dataBZ = dataArray.datenB.datenBZ;
	let dataBE = dataArray.datenB.datenBE;
	let dataE = dataArray.datenE;
	let dataN = dataArray.datenN;

	const vorhanden: string[] = [];

	let monatswechsel = false;

	if (Storage.check("monatswechsel")) {
		monatswechsel = true;
		Storage.remove("monatswechsel");
	}

	let dataServer: { [key: string]: unknown } = {};
	if (Storage.check("dataServer")) {
		dataServer = Storage.get("dataServer");
		console.log("Unterschiede Server - Client | Bereits vorhanden", dataServer);
	}

	const saveDaten = <T>(
		nameStorage: string,
		name: string,
		data: T,
		dataName: string,
		beschreibung: string,
		loadTable: false | { name: string; data: { [key: string]: unknown }[] } = false
	): T => {
		if (!Storage.check(nameStorage)) {
			console.log(`${name} speichern, nicht vorhanden`);
			Storage.set(nameStorage, data);
			if (loadTable)
				document.querySelector<CustomHTMLTableElement>(`#${loadTable.name}`)?.instance.rows.load(loadTable.data);
		} else if (monatswechsel) {
			console.log(`${name} speichern`);
			Storage.set(nameStorage, data);
		} else if (!Storage.compare(nameStorage, data)) {
			console.log(`${name} vorhanden & nderungen`);
			if (!vorhanden.find(element => element == beschreibung)) vorhanden.push(beschreibung);
			dataServer[dataName] = data;
			return Storage.get<T>(nameStorage);
		}
		return data;
	};
	vorgabenU = saveDaten("VorgabenU", "VorgabenU", vorgabenU, "vorgabenU", "Persnliche Daten", {
		name: "tableVE",
		data: [...Object.values(vorgabenU.vorgabenB)],
	});
	const willkommen = document.querySelector<HTMLHeadingElement>("#Willkommen");
	if (willkommen) {
		willkommen.innerHTML = `Hallo, ${vorgabenU.pers.Vorname}.`;
	}
	dataBZ = saveDaten("dataBZ", "DatenBZ", dataBZ, "dataBZ", "Bereitschaftszeit", {
		name: "tableBZ",
		data: DataBZ(dataBZ),
	});
	dataBE = saveDaten("dataBE", "DatenBE", dataBE, "dataBE", "Bereitschaftseinsatz", {
		name: "tableBE",
		data: DataBE(dataBE),
	});
	dataE = saveDaten("dataE", "DatenE", dataE, "dataE", "EWT", {
		name: "tableE",
		data: DataE(dataE),
	});
	dataN = saveDaten("dataN", "DatenN", dataN, "dataN", "Nebenbezge", {
		name: "tableN",
		data: DataN(dataN),
	});

	Storage.set("datenBerechnung", datenBerechnung);
	Storage.set("VorgabenGeld", datenGeld);

	if (vorhanden.length > 0) {
		if (Object.keys(dataServer).length > 0) console.log("Unterschiede Server - Client", dataServer);
		Storage.set("dataServer", dataServer);

		createSnackBar({
			message: `<b>Ungespeicherte Daten:</b> <br/>${vorhanden.join("<br/>")}`,
			status: "info",
			dismissible: false,
			timeout: false,
			position: "br",
			fixed: true,
			actions: [
				{
					text: "Lokale Daten berschreiben",
					function: () => {
						SaveUserDatenUEberschreiben();
						clearLoading("btnAuswaehlen");
						buttonDisable(false);
					},
					dismiss: true,
					class: ["text-primary"],
				},
				{
					text: "Daten behalten",
					function: () => {
						SaveUserDatenUEberschreiben(false);
						clearLoading("btnAuswaehlen");
						buttonDisable(false);
					},
					dismiss: true,
					class: ["text-secondary"],
				},
			],
		});
	} else {
		clearLoading("btnAuswaehlen");
		buttonDisable(false);
	}

	if (dataServer.dataBZ != null || monatswechsel)
		document.querySelector<CustomHTMLTableElement>("#tableBZ")?.instance.rows.load(DataBZ(dataBZ));
	if (dataServer.dataBE != null || monatswechsel)
		document.querySelector<CustomHTMLTableElement>("#tableBE")?.instance.rows.load(DataBE(dataBE));
	if (dataServer.dataE != null || monatswechsel)
		document.querySelector<CustomHTMLTableElement>("#tableE")?.instance.rows.load(DataE(dataE));
	if (dataServer.dataN != null || monatswechsel)
		document.querySelector<CustomHTMLTableElement>("#tableN")?.instance.rows.load(DataN(dataN));
	if (dataServer.dataVE != null || monatswechsel)
		document
			.querySelector<CustomHTMLTableElement>("#tableVE")
			?.instance.rows.load([...Object.values(vorgabenU.vorgabenB)]);

	generateTableBerechnung(datenBerechnung, datenGeld);
	generateEingabeMaskeEinstellungen(vorgabenU);

	if (vorgabenU.pers.TB == "Tarifkraft")
		(<HTMLLIElement>document.querySelector<HTMLButtonElement>("#Neben-tab")?.parentElement).classList.remove("d-none");
	document.querySelector<HTMLDivElement>("#navmenu")?.classList.remove("d-none");
	document.querySelector<HTMLButtonElement>("#btn-navmenu")?.classList.remove("d-none");
	createSnackBar({
		message: `Neue Daten geladen.`,
		status: "success",
		timeout: 3000,
		position: "br",
		fixed: true,
	});
}

function setMonatJahr(jahr: number, monat: number): void {
	const inputMonat = document.querySelector<HTMLInputElement>("#Monat");
	const headingMonatB = document.querySelector<HTMLHeadingElement>("#MonatB");
	const headingMonatE = document.querySelector<HTMLHeadingElement>("#MonatE");
	const headingMonatN = document.querySelector<HTMLHeadingElement>("#MonatN");
	const headingMonatBerechnung = document.querySelector<HTMLHeadingElement>("#MonatBerechnung");

	if (!inputMonat || !headingMonatB || !headingMonatE || !headingMonatN || !headingMonatBerechnung) {
		throw new Error("One or more elements not found.");
	}

	inputMonat.value = monat.toString();
	const datum = dayjs([+jahr, monat - 1]);
	headingMonatB.innerText = headingMonatE.innerText = headingMonatN.innerText = datum.format("MM / YY");
	headingMonatBerechnung.innerText = jahr.toString();
}

function SaveUserDatenUEberschreiben(ueberschreiben = true): void {
	if (!ueberschreiben) {
		Storage.remove("dataServer");
		return;
	}
	const dataServer: {
		vorgabenU?: IVorgabenU;
		datenGeld?: IVorgabenGeld;
		datenBerechnung?: IVorgabenBerechnung;
		dataBZ?: IDaten["BZ"];
		dataBE?: IDaten["BE"];
		dataE?: IDaten["EWT"];
		dataN?: IDaten["N"];
	} = Storage.get("dataServer");
	console.log({ dataServer });
	if (dataServer.vorgabenU) {
		console.log("VorgabenU berschreiben");
		Storage.set("VorgabenU", dataServer.vorgabenU);
		document
			.querySelector<CustomHTMLTableElement>("#tableVE")
			?.instance.rows.load([...Object.values(dataServer.vorgabenU.vorgabenB)]);
		generateEingabeMaskeEinstellungen(dataServer.vorgabenU);
		delete dataServer.vorgabenU;
	}
	if (dataServer.dataBZ) {
		console.log("DatenBZ berschreiben");
		Storage.set("dataBZ", dataServer.dataBZ);
		document.querySelector<CustomHTMLTableElement>("#tableBZ")?.instance.rows.load(DataBZ(dataServer.dataBZ));
		delete dataServer.dataBZ;
	}
	if (dataServer.dataBE) {
		console.log("DatenBE berschreiben");
		Storage.set("dataBE", dataServer.dataBE);
		document.querySelector<CustomHTMLTableElement>("#tableBE")?.instance.rows.load(DataBE(dataServer.dataBE));
		delete dataServer.dataBE;
	}
	if (dataServer.dataE) {
		console.log("DatenE berschreiben");
		Storage.set("dataE", dataServer.dataE);
		document.querySelector<CustomHTMLTableElement>("#tableE")?.instance.rows.load(DataE(dataServer.dataE));
		delete dataServer.dataE;
	}
	if (dataServer.dataN) {
		console.log("DatenN berschreiben");
		Storage.set("dataN", dataServer.dataN);
		document.querySelector<CustomHTMLTableElement>("#tableN")?.instance.rows.load(DataN(dataServer.dataN));
		delete dataServer.dataN;
	}

	Storage.remove("dataServer");
}
