import { Modal } from "bootstrap";
import dayjs from "dayjs";
import "dayjs/locale/de.js";
import arraySupport from "dayjs/plugin/arraySupport.js";
import customParseFormat from "dayjs/plugin/customParseFormat.js";
import LocalizedFormat from "dayjs/plugin/localizedFormat.js";
import { buttonDisable, checkMaxTag, download, saveDaten, saveTableData, Storage, tableToArray } from "./Core";
import { createSnackBar } from "./CustomSnackbar";
import { Column, createCustomTable, CustomTable, Row } from "./CustomTable";
import { CustomHTMLDivElement, CustomHTMLTableElement, IDaten } from "./Interfaces";
import {
	createEditorModalFooter,
	createModal,
	createModalBodyInputElement,
	createModalBodySelectElement,
	createModalBodyShowElement,
	createModalBodySpanElement,
	createModalBodyTitelElement,
	createShowModalFooter,
} from "./ModalComponents";
dayjs.extend(arraySupport);
dayjs.extend(customParseFormat);
dayjs.extend(LocalizedFormat);
window.addEventListener("load", () => {
	const ftN = createCustomTable("tableN", {
		columns: [
			{ name: "tagN", title: "Tag", sortable: true, sorted: true, direction: "ASC" },
			{ name: "beginN", title: "Arbeit Von", type: "time" },
			{ name: "endeN", title: "Arbeit Bis", type: "time" },
			{ name: "beginPauseN", title: "Pause Von", breakpoints: "sm", type: "time" },
			{ name: "endePauseN", title: "Pause Bis", breakpoints: "sm", type: "time" },
			{ name: "dauerN", title: "Anzahl", breakpoints: "md" },
			{ name: "nrN", title: "Zulage", breakpoints: "md" },
		],
		rows: DataN(),
		sorting: { enabled: true },
		editing: {
			enabled: true,
			addRow: () => {
				const $modal = createEditorModalNeben(ftN, "Nebenbezug hinzufgen");
				$modal.row = ftN;
				new Modal($modal).show();
			},
			editRow: row => {
				const $modal = createEditorModalNeben(row, "Nebenbezug bearbeiten");
				$modal.row = row;
				new Modal($modal).show();
			},
			showRow: row => {
				const $modal = createShowModalNeben(row, "Nebenbezug anzeigen");
				$modal.row = row;
				new Modal($modal).show();
			},
			deleteRow: row => {
				row.deleteRow();
				saveTableData("tableN", ftN);
			},
			deleteAllRows: () => {
				createSnackBar({
					message: "Mchtest du wirklich alle Zeilen lschen?",
					icon: "question",
					status: "error",
					dismissible: false,
					timeout: false,
					position: "br",
					fixed: true,
					actions: [
						{
							text: "Ja",
							function: () => {
								ftN.rows.load([]);
								buttonDisable(false);
								Storage.set("dataBE", []);
							},
							dismiss: true,
							class: ["text-danger"],
						},
						{ text: "Nein", dismiss: true, class: ["text-primary"] },
					],
				});
			},
		},
	});
	const btnESN = document.querySelector<HTMLButtonElement>("#btnESN");
	if (btnESN) {
		btnESN.addEventListener("click", () => {
			createAddModalNeben();
		});
	} else {
		throw new Error("Button with ID 'btnESN' not found.");
	}

	const btnSaveN = document.querySelector<HTMLButtonElement>("#btnSaveN");
	if (btnSaveN) {
		btnSaveN.addEventListener("click", () => {
			saveDaten(btnSaveN);
		});
	} else {
		throw new Error("Button with ID 'btnSaveN' not found.");
	}

	const btnDownloadN = document.querySelector<HTMLButtonElement>("#btnDownloadN");
	if (btnDownloadN) {
		btnDownloadN.addEventListener("click", () => {
			download(btnDownloadN, "N");
		});
	} else {
		throw new Error("Button with ID 'btnDownloadN' not found.");
	}
});

function createAddModalNeben(): void {
	const { modal, form } = createModal(
		"Neuen Nebenbezug eingeben",
		true,
		null,
		createBodyElement,
		createEditorModalFooter([customFooterButtton()]),
		SubmitEventListener
	);
	new Modal(modal).show();

	function createBodyElement(): HTMLDivElement {
		const dataE: IDaten["EWT"] = tableToArray("tableE");
		if (dataE.length === 0) {
			createSnackBar({
				message: 'Keine Tage in EWT gefunden. </br></br>Bitte erst EWT ausfllen! </br>oder Manuell ber "Neue Zeile"',
				timeout: 3000,
				position: "br",
				fixed: true,
			});
			throw new Error("'Keine Tage in EWT gefunden.");
		}
		console.log(dataE);

		const modalBody = document.createElement("div");
		modalBody.className = "modal-body";

		const warnung = document.createElement("p");
		warnung.className = "text-center";
		warnung.textContent = "!!! Erst EWT Eingeben und Berechnen !!!";
		modalBody.appendChild(warnung);

		modalBody.appendChild(
			createModalBodySelectElement({
				divClass: "form-floating col mb-3",
				title: "Tag (Aus EWT)",
				name: "tagN",
				required: true,
				value: null,
				options: getTagOptions(),
			})
		);

		modalBody.appendChild(
			createModalBodySelectElement({
				divClass: "form-floating col mb-3",
				title: "Nebenbezug (Aktuell nur 1 mglichkeit)",
				name: "Nebenbezug",
				required: true,
				value: null,
				options: [
					{
						value: "040 Fahrentsch.",
						text: "040 Fahrentsch.",
						selected: true,
					},
				],
			})
		);

		modalBody.appendChild(
			createModalBodyInputElement({
				divClass: "form-floating col",
				title: "Anzahl",
				name: "AnzahlN",
				value: 1,
				type: "number",
				required: true,
				min: 1,
				max: 1,
			})
		);

		return modalBody;

		function getTagOptions(): {
			value: string | number;
			text: string;
			disabled: boolean | undefined;
			selected: boolean | undefined;
		}[] {
			const dataN: IDaten["N"] = tableToArray("tableN");
			const jahr = Storage.get<number>("Jahr");
			const monat = Storage.get<number>("Monat");
			const options = [];
			for (const day of dataE) {
				const schicht = day.schichtE;
				let tagN: string,
					tag: string,
					pauseA = "12:00",
					pauseE = "12:30";
				if (["N", "BN"].includes(schicht)) {
					tagN = `0${+day.tagE - 1}`.slice(-2);
					tag = `${tagN} | ${new Date(jahr, monat - 1, +day.tagE - 1).toLocaleDateString("de", {
						weekday: "short",
					})}`;
					pauseA = "01:00";
					pauseE = "01:45";
				} else {
					tagN = `0${day.tagE}`.slice(-2);
					tag = `${tagN} | ${new Date(jahr, monat - 1, +day.tagE).toLocaleDateString("de", {
						weekday: "short",
					})}`;
					if (tag.includes("Fr")) {
						pauseA = "";
						pauseE = "";
					}
				}

				const option = {} as {
					value: string | number;
					text: string;
					disabled: boolean | undefined;
					selected: boolean | undefined;
				};
				if (schicht == "N") {
					option.text = `${tag} | Nacht`;
				} else if (schicht == "BN") {
					option.text = `${tag} | Nacht / Bereitschaft`;
				} else {
					option.text = tag;
				}
				option.value = JSON.stringify({
					tagN,
					beginN: day.beginE,
					endeN: day.endeE,
					beginPauseN: pauseA,
					endePauseN: pauseE,
					nrN: "",
					dauerN: 0,
				});
				if (dataN) {
					dataN.forEach(value => {
						if (Number(value.tagN) == Number(tagN)) {
							option.disabled = true;
						}
					});
				}
				options.push(option);
			}
			return options;
		}
	}

	function customFooterButtton(): HTMLButtonElement {
		const btnManuell = document.createElement("button");
		btnManuell.className = "btn btn-info";
		btnManuell.type = "button";
		btnManuell.textContent = "Manuell";
		btnManuell.ariaLabel = "Manuell";
		btnManuell.dataset.bsDismiss = "modal";
		btnManuell.addEventListener("click", () => {
			const table = document.querySelector<CustomHTMLTableElement>("#tableN");
			if (!table) throw new Error("table N nicht gefunden");

			table.instance.options.editing.addRow();
		});
		return btnManuell;
	}

	function SubmitEventListener(): (event: Event) => void {
		return (event: Event) => {
			if (form instanceof HTMLFormElement && !form.checkValidity()) return;
			event.preventDefault();

			addNebenTag(form);
		};
	}
}
function createEditorModalNeben(row: Row | CustomTable, titel: string): CustomHTMLDivElement {
	const { modal, form } = createModal(
		titel,
		true,
		null,
		createBodyElement,
		createEditorModalFooter(null, row instanceof Row ? "Speichern" : null),
		SubmitEventListener
	);

	return modal;

	function createBodyElement(): HTMLDivElement {
		const modalBody = document.createElement("div");
		modalBody.className = "modal-body row";

		const Monat: number = Storage.get<number>("Monat") - 1;
		const Jahr: number = Storage.get<number>("Jahr");

		let Tag: number;
		if (row instanceof Row) {
			Tag = row.cells.tagN;
		} else if (row instanceof CustomTable) {
			Tag = checkMaxTag(Jahr, Monat);
		} else throw new Error("unbekannter Fehler");

		const datum = dayjs([Jahr, Monat, Tag]);

		let column;

		column = row.columns.array.find(column => column.name === "tagN") as Column;
		modalBody.appendChild(
			createModalBodyInputElement({
				divClass: "form-floating col-6 pb-3",
				title: column.title,
				name: column.name,
				value: datum.format("YYYY-MM-DD"),
				type: "date",
				required: true,
				min: datum.startOf("M").format("YYYY-MM-DD"),
				max: datum.endOf("M").format("YYYY-MM-DD"),
			})
		);

		modalBody.appendChild(createModalBodyTitelElement("Arbeitszeit"));

		["beginN", "endeN"].forEach(value => {
			column = row.columns.array.find(column => column.name === value) as Column;
			modalBody.appendChild(
				createModalBodyInputElement({
					divClass: "form-floating col-6 pb-3",
					title: column.title,
					name: column.name,
					value: row instanceof Row ? row.cells[column.name] : null,
					type: "time",
					required: true,
				})
			);
		});

		modalBody.appendChild(createModalBodyTitelElement("Pause"));

		["beginPauseN", "endePauseN"].forEach(value => {
			column = row.columns.array.find(column => column.name === value) as Column;
			modalBody.appendChild(
				createModalBodyInputElement({
					divClass: "form-floating col-6 pb-3",
					title: column.title,
					name: column.name,
					value: row instanceof Row ? row.cells[column.name] : null,
					type: "time",
				})
			);
		});

		modalBody.appendChild(createModalBodyTitelElement("Zulage"));

		column = row.columns.array.find(column => column.name === "dauerN") as Column;
		modalBody.appendChild(
			createModalBodyInputElement({
				divClass: "form-floating col-6 pb-3",
				title: column.title,
				name: column.name,
				value: row instanceof Row ? row.cells[column.name] : 1,
				type: "number",
				required: true,
				min: 1,
				max: 1,
			})
		);

		column = row.columns.array.find(column => column.name === "nrN") as Column;
		modalBody.appendChild(
			createModalBodySelectElement({
				divClass: "form-floating col-6 pb-3",
				title: column.title,
				name: column.name,
				value: row instanceof Row ? row.cells[column.name] : null,
				required: true,
				options: [{ value: "040 Fahrentsch.", text: "040 Fahrentsch.", selected: true }],
			})
		);

		return modalBody;
	}

	function SubmitEventListener(): (event: Event) => void {
		return (event: Event) => {
			if (!(form instanceof HTMLFormElement)) return;
			event.preventDefault();
			if (!form.checkValidity()) return;

			const row = modal.row;
			if (!row) throw new Error("Row nicht gefunden");
			const table = row instanceof Row ? row.CustomTable : row;

			const values: {
				tagN: string;
				beginN: string;
				endeN: string;
				beginPauseN: string;
				endePauseN: string;
				nrN: string;
				dauerN: number;
			} = {
				tagN: dayjs(form.querySelector<HTMLInputElement>("#tagN")?.value ?? 0).format("DD"),
				beginN: form.querySelector<HTMLInputElement>("#beginN")?.value ?? "",
				endeN: form.querySelector<HTMLInputElement>("#endeN")?.value ?? "",
				beginPauseN: form.querySelector<HTMLInputElement>("#beginPauseN")?.value ?? "",
				endePauseN: form.querySelector<HTMLInputElement>("#endePauseN")?.value ?? "",
				nrN: form.querySelector<HTMLInputElement>("#nrN")?.value ?? "",
				dauerN: Number(form.querySelector<HTMLInputElement>("#dauerN")?.value) ?? 0,
			};
			row instanceof Row ? row.val(values) : row.rows.add(values);

			(<Modal>Modal.getInstance(modal)).hide();
			saveTableData("tableN", table);
		};
	}
}
function createShowModalNeben(row: Row, title: string): CustomHTMLDivElement {
	const { modal } = createModal(title, false, "sm", createModalBody, createShowModalFooter);
	return modal;

	function createModalBody() {
		const modalBody = document.createElement("div");
		modalBody.className = "modal-body row";
		let column1, column2;

		column1 = row.columns.array.find(column => column.name === "tagN") as Column;
		modalBody.appendChild(
			createModalBodyShowElement({
				divClass: "mb-2 col-12 text-center",
				labelClass: "pe-3 align-middle col-form-label text-wrap fw-bold",
				spanClass: "align-middle my-auto",
				title: `${column1.title}:`,
				name: column1.name,
				text: column1.parser(row.cells[column1.name]),
			})
		);

		modalBody.appendChild(createModalBodyTitelElement("Arbeitszeit"));

		column1 = row.columns.array.find(column => column.name === "beginN") as Column;
		column2 = row.columns.array.find(column => column.name === "endeN") as Column;
		modalBody.appendChild(
			createModalBodyShowElementNeben({
				divClass: "mb-2 col-12 text-center",
				vorTitle: column1.parser(row.cells[column1.name]) as string,
				title: "arrow_right_alt",
				nachTitle: column2.parser(row.cells[column2.name]) as string,
			})
		);

		modalBody.appendChild(createModalBodyTitelElement("Pause"));

		column1 = row.columns.array.find(column => column.name === "beginPauseN") as Column;
		column2 = row.columns.array.find(column => column.name === "endePauseN") as Column;
		modalBody.appendChild(
			createModalBodyShowElementNeben({
				divClass: "mb-2 text-center",
				vorTitle: column1.parser(row.cells[column1.name]) as string,
				title: "arrow_right_alt",
				nachTitle: column2.parser(row.cells[column2.name]) as string,
			})
		);

		modalBody.appendChild(createModalBodyTitelElement("Zulage"));

		column1 = row.columns.array.find(column => column.name === "dauerN") as Column;
		column2 = row.columns.array.find(column => column.name === "nrN") as Column;
		modalBody.appendChild(
			createModalBodyShowElementNeben({
				divClass: "mb-2 col-12 text-center",
				vorTitleClass: "col-4 align-middle text-break my-auto",
				vorTitle: column1.parser(row.cells[column1.name]) as string,
				titleClass: "align-middle fw-semibold mx-1",
				title: "X",
				nachTitleClass: "col-6 align-middle text-break my-auto",
				nachTitle: column2.parser(row.cells[column2.name]) as string,
			})
		);

		return modalBody;

		function createModalBodyTitelElement(text: string): HTMLHeadingElement {
			const title = document.createElement("h4");
			title.className = "text-center mb-0";
			title.innerHTML = text;
			return title;
		}

		function createModalBodyShowElementNeben(options: {
			divClass?: string;
			vorTitle: string;
			title: string;
			nachTitle: string;
			vorTitleClass?: string;
			titleClass?: string;
			nachTitleClass?: string;
		}): HTMLDivElement {
			const showWrapper = document.createElement("div");
			showWrapper.className = options.divClass ?? "icon-ewt";

			showWrapper.appendChild(createModalBodySpanElement(options.vorTitleClass ?? "", options.vorTitle));
			showWrapper.appendChild(
				createModalBodySpanElement(options.titleClass ?? "material-icons-round big-icons", options.title)
			);
			showWrapper.appendChild(createModalBodySpanElement(options.nachTitleClass ?? "", options.nachTitle));

			return showWrapper;
		}
	}
}

export function DataN(data?: IDaten["N"]): IDaten["N"] {
	if (data === undefined) {
		if (!Storage.check("dataN")) return [];
		data = Storage.get<IDaten["N"]>("dataN");
	}
	if (data === undefined) return [];
	return data;
}

function addNebenTag(form: HTMLDivElement | HTMLFormElement): void {
	const select = form.querySelector<HTMLSelectElement>("#tagN");
	if (!select) {
		throw new Error("Select element with ID 'tagN' not found");
	}
	let idN = select.selectedIndex;
	if (idN < 0) return;
	const daten = JSON.parse(select.value) as Required<IDaten>["N"][0];
	const inputNebenbezug = form.querySelector<HTMLSelectElement>("#Nebenbezug");
	if (!inputNebenbezug) {
		throw new Error("Select element with ID 'Nebenbezug' not found");
	}
	daten.nrN = inputNebenbezug.value;
	const inputAnzahlN = form.querySelector<HTMLInputElement>("#AnzahlN");
	if (!inputAnzahlN) {
		throw new Error("Input element with ID 'AnzahlN' not found");
	}
	daten.dauerN = +inputAnzahlN.value;

	console.log(daten);
	select.options[idN].selected = false;
	select.options[idN].disabled = true;
	idN++;
	while (idN < select.length) {
		if (!select.options[idN].disabled) {
			select.options[idN].selected = true;
			break;
		}
		idN++;
	}

	const tableN = document.querySelector<CustomHTMLTableElement>("#tableN");
	if (!tableN) throw new Error("table N nicht gefunden");
	const ftN = tableN.instance;
	ftN.rows.add(daten);
	saveTableData("tableN", ftN);
}
