window.addEventListener("load", () => {
	const datetimeParser = (value: string): string => dayjs(value).format("DD.MM.YYYY, LT"),
		timeZeroParser = (value: number): number | string => (!value ? "" : value),
		ftBZ = createCustomTable("tableBZ", {
			columns: [
				{ name: "beginB", title: "Von", parser: datetimeParser, sortable: true, sorted: true, direction: "ASC" },
				{ name: "endeB", title: "Bis", parser: datetimeParser, sortable: true },
				{ name: "pauseB", title: "Pause", parser: timeZeroParser, breakpoints: "xs" },
			],
			rows: DataBZ(),
			sorting: { enabled: true },
			editing: {
				enabled: true,
				addRow: () => {
					const $modal = createEditorModalBereitschaftsZeit(ftBZ, "Zeitraum hinzufgen");
					$modal.row = ftBZ;
					new Modal($modal).show();
				},
				editRow: row => {
					const $modal = createEditorModalBereitschaftsZeit(row, "Zeitraum bearbeiten");
					$modal.row = row;
					new Modal($modal).show();
				},
				showRow: row => {
					const $modal = createShowModalBereitschaft(row, "Zeitraum anzeigen");
					$modal.row = row;
					new Modal($modal).show();
				},
				deleteRow: row => {
					row.deleteRow();
					saveTableData("tableBZ", ftBZ);
				},
				deleteAllRows: () => {
					createSnackBar({
						message: "Mchtest du wirklich alle Zeilen lschen?",
						icon: "question",
						status: "error",
						dismissible: false,
						timeout: false,
						position: "br",
						fixed: true,
						actions: [
							{
								text: "Ja",
								function: () => {
									ftBZ.rows.load([]);
									buttonDisable(false);
									Storage.set("dataBZ", []);
								},
								dismiss: true,
								class: ["text-danger"],
							},
							{ text: "Nein", dismiss: true, class: ["text-primary"] },
						],
					});
				},
			},
		});

	// ----------------------------- Bereitschaftseinstze ------------------------------------------------

	const ftBE = createCustomTable("tableBE", {
		columns: [
			{ name: "tagBE", title: "Tag", sortable: true, sorted: true, direction: "ASC" },
			{ name: "auftragsnummerBE", title: "Auftrags-Nr.", classes: ["custom-text-truncate"] },
			{ name: "beginBE", title: "Von", breakpoints: "sm", type: "time" },
			{ name: "endeBE", title: "Bis", breakpoints: "sm", type: "time" },
			{ name: "lreBE", title: "LRE" },
			{ name: "privatkmBE", title: "Privat Km", parser: timeZeroParser, breakpoints: "md" },
		],
		rows: DataBE(),
		sorting: { enabled: true },
		editing: {
			enabled: true,
			addRow: () => {
				const $modal = createEditorModalBereitschaftsEinsatz(ftBE, "Einsatz hinzufgen");
				$modal.row = ftBE;
				new Modal($modal).show();
			},
			editRow: row => {
				const $modal = createEditorModalBereitschaftsEinsatz(row, "Einsatz bearbeiten");
				$modal.row = row;
				new Modal($modal).show();
			},
			showRow: row => {
				const $modal = createShowModalBereitschaft(row, "Einsatz anzeigen");
				$modal.row = row;
				new Modal($modal).show();
			},
			deleteRow: row => {
				row.deleteRow();
				saveTableData("tableBE", ftBE);
			},
			deleteAllRows: () => {
				createSnackBar({
					message: "Mchtest du wirklich alle Zeilen lschen?",
					icon: "question",
					status: "error",
					dismissible: false,
					timeout: false,
					position: "br",
					fixed: true,
					actions: [
						{
							text: "Ja",
							function: () => {
								ftBE.rows.load([]);
								buttonDisable(false);
								Storage.set("dataBE", []);
							},
							dismiss: true,
							class: ["text-danger"],
						},
						{ text: "Nein", dismiss: true, class: ["text-primary"] },
					],
				});
			},
		},
	});

	// "click"-Eventlistener

	const btnESZ = document.querySelector<HTMLButtonElement>("#btnESZ");
	const btnESE = document.querySelector<HTMLButtonElement>("#btnESE");
	const btnSaveB = document.querySelector<HTMLButtonElement>("#btnSaveB");
	const btnDownloadB = document.querySelector<HTMLButtonElement>("#btnDownloadB");

	if (btnESZ) {
		btnESZ.addEventListener("click", createAddModalBereitschaftsZeit);
	}

	if (btnESE) {
		btnESE.addEventListener("click", createAddModalBereitschaftsEinsatz);
	}

	if (btnSaveB) {
		btnSaveB.addEventListener("click", () => {
			saveDaten(btnSaveB);
		});
	}

	if (btnDownloadB) {
		btnDownloadB.addEventListener("click", () => {
			download(btnDownloadB, "B");
		});
	}
});

// create Modal

function createAddModalBereitschaftsZeit(): void {
	const { modal, form } = createModal(
		"Neue Bereitschaft eingeben",
		true,
		null,
		createBodyElement,
		createEditorModalFooter(),
		SubmitEventListener
	);
	new Modal(modal).show();

	function createBodyElement(): HTMLDivElement {
		const modalBody = document.createElement("div");
		modalBody.className = "modal-body row";

		const vorgabenU = Storage.get<Partial<IVorgabenU>>("VorgabenU", { vorgabenB: BereitschaftsEinsatzZeitrume }),
			Monat = Storage.get<number>("Monat") - 1,
			Jahr = Storage.get<number>("Jahr");
		const vorgabenB: IVorgabenU["vorgabenB"] = vorgabenU.vorgabenB ? vorgabenU.vorgabenB : BereitschaftsEinsatzZeitrume;

		let vorgabenBStandardIndex = "2";
		for (const key in vorgabenB) {
			if (vorgabenB[key].standard) {
				vorgabenBStandardIndex = key;
				break;
			}
		}

		const vorgabeB_Div = createModalBodySelectElement({
			divClass: "form-floating col-12 pb-3",
			title: "Auswahl Bereitschaft",
			name: "vorgabeB",
			value: vorgabenBStandardIndex,
			options: Object.entries(vorgabenB).map(value => {
				return {
					value: value[0],
					html: false,
					text:
						`${value[1].Name} | ` +
						`${dayjs().day(value[1].beginnB.tag).format("ddd")} - ${dayjs().day(value[1].endeB.tag).format("ddd")} | ` +
						`${
							value[1].nacht
								? `${dayjs().day(value[1].beginnN.tag).format("ddd")} - ${dayjs().day(value[1].endeN.tag).format("ddd")}`
								: "-----"
						}` +
						(value[1].standard ? " | Standard" : ""),
				};
			}),
			eventListener: () => {
				if (!vorgabeB_Select) throw new Error("VorgabeB Input nicht gefunden");
				bereitschaftsVorgabeAEndern(modalBody, vorgabenB[vorgabeB_Select.value]);
			},
		});
		modalBody.appendChild(vorgabeB_Div);

		const vorgabeB_Select = vorgabeB_Div.querySelector<HTMLSelectElement>("#vorgabeB");
		if (!vorgabeB_Select) throw new Error("VorgabeB Input nicht gefunden");

		const auswahl = vorgabeB_Select.value;

		let datum = dayjs([Jahr, Monat, checkMaxTag(Jahr, Monat)]).isoWeekday(vorgabenB[auswahl].beginnB.tag);
		if (datum.isSameOrBefore(dayjs([Jahr, Monat]).startOf("M"))) {
			datum = datum.add(1, "w");
		} else if (datum.isSameOrAfter(dayjs([Jahr, Monat]).endOf("M"))) {
			datum = datum.subtract(1, "w");
		}

		modalBody.appendChild(document.createElement("hr"));

		modalBody.appendChild(createModalBodyTitelElement("Bereitschafts Anfang"));
		modalBody.appendChild(
			createModalBodyInputElement({
				divClass: "form-floating col-7 col-sm-6 pb-3",
				title: "Datum",
				name: "bA",
				value: datum.format("YYYY-MM-DD"),
				type: "Date",
				required: true,
				min: datum.startOf("M").format("YYYY-MM-DD"),
				max: datum.endOf("M").format("YYYY-MM-DD"),
				eventListener: () => {
					const datum = modalBody.querySelector<HTMLInputElement>("#bA");
					if (!datum) throw new Error("Datum nicht gefunden");
					datumAnpassen(modalBody, vorgabenB[vorgabeB_Select.value], dayjs(datum.value));
				},
			})
		);
		modalBody.appendChild(
			createModalBodyInputElement({
				divClass: "form-floating col-5 col-sm-6 pb-3",
				title: "Von",
				name: "bAT",
				value: vorgabenB[auswahl].beginnB.zeit,
				type: "time",
				required: true,
				disabled: true,
			})
		);
		modalBody.appendChild(createModalBodyTitelElement("Bereitschafts Ende"));
		modalBody.appendChild(
			createModalBodyInputElement({
				divClass: "form-floating col-7 col-sm-6 pb-3",
				title: "Datum",
				name: "bE",
				value: datum
					.isoWeekday(vorgabenB[auswahl].endeB.tag)
					.add(vorgabenB[auswahl].endeB.Nwoche ? 7 : 0, "d")
					.format("YYYY-MM-DD"),
				type: "Date",
				required: true,
				disabled: true,
				min: datum.startOf("M").format("YYYY-MM-DD"),
				max: datum.add(1, "M").endOf("M").format("YYYY-MM-DD"),
			})
		);
		modalBody.appendChild(
			createModalBodyInputElement({
				divClass: "form-floating col-5 col-sm-6 pb-3",
				title: "Bis",
				name: "bET",
				value: vorgabenB[auswahl].endeB.zeit,
				type: "time",
				required: true,
				disabled: true,
			})
		);
		modalBody.appendChild(document.createElement("hr"));
		modalBody.appendChild(
			createModalBodyCheckbox({
				checkClass: "form-check form-switch bereitschaft",
				id: "nacht",
				text: "Nachtschicht",
				status: vorgabenB[auswahl].nacht,
				eventListener: () => nachtAusblenden(modalBody),
			})
		);

		const nachtWrapper = document.createElement("div");
		nachtWrapper.id = "nachtschicht";
		nachtWrapper.className = "row m-0 p-0";
		if (!vorgabenB[auswahl].nacht) nachtWrapper.style.display = "none";
		modalBody.appendChild(nachtWrapper);

		nachtWrapper.appendChild(createModalBodyTitelElement("Nacht Anfang"));
		nachtWrapper.appendChild(
			createModalBodyInputElement({
				divClass: "form-floating col-7 col-sm-6 pb-3",
				title: "Datum",
				name: "nA",
				value: datum
					.isoWeekday(vorgabenB[auswahl].beginnN.tag)
					.add(vorgabenB[auswahl].beginnN.Nwoche ? 7 : 0, "d")
					.format("YYYY-MM-DD"),
				type: "Date",
				required: true,
				disabled: true,
				min: datum.startOf("M").format("YYYY-MM-DD"),
				max: datum.add(1, "M").endOf("M").format("YYYY-MM-DD"),
			})
		);
		nachtWrapper.appendChild(
			createModalBodyInputElement({
				divClass: "form-floating col-5 col-sm-6 pb-3",
				title: "Von",
				name: "nAT",
				value: vorgabenB[auswahl].beginnN.zeit,
				type: "time",
				required: true,
				disabled: true,
			})
		);

		nachtWrapper.appendChild(createModalBodyTitelElement("Nacht Ende"));
		nachtWrapper.appendChild(
			createModalBodyInputElement({
				divClass: "form-floating col-7 col-sm-6 pb-3",
				title: "Datum",
				name: "nE",
				value: datum
					.isoWeekday(vorgabenB[auswahl].endeN.tag)
					.add(vorgabenB[auswahl].endeN.Nwoche ? 7 : 0, "d")
					.format("YYYY-MM-DD"),
				type: "Date",
				required: true,
				disabled: true,
				min: datum.startOf("M").format("YYYY-MM-DD"),
				max: datum.add(1, "M").endOf("M").format("YYYY-MM-DD"),
			})
		);
		nachtWrapper.appendChild(
			createModalBodyInputElement({
				divClass: "form-floating col-5 col-sm-6 pb-3",
				title: "Bis",
				name: "nET",
				value: vorgabenB[auswahl].endeN.zeit,
				type: "time",
				required: true,
				disabled: true,
			})
		);
		const customHrElement = document.createElement("hr");
		customHrElement.classList.add("mt-3");

		modalBody.appendChild(customHrElement);
		modalBody.appendChild(
			createModalBodyCheckbox({
				checkClass: "form-check form-switch bereitschaft",
				id: "eigen",
				text: "Eingabe Eigene Werte?",
				status: false,
				eventListener: () => {
					const datum = modalBody.querySelector<HTMLInputElement>("#bA");
					if (!datum) throw new Error("Datum nicht gefunden");
					eigeneWerte(modalBody, vorgabenB[vorgabeB_Select.value], dayjs(datum.value));
				},
			})
		);

		return modalBody;
	}

	function SubmitEventListener(): (event: Event) => void {
		return (event: Event) => {
			if (form instanceof HTMLFormElement && !form.checkValidity()) return;
			event.preventDefault();

			bereitschaftEingabeWeb(modal, Storage.get("accessToken"));

			const table = document.querySelector<CustomHTMLTableElement>("#tableBZ");

			(<Modal>Modal.getInstance(modal)).hide();

			if (table) {
				saveTableData("tableBZ", table.instance);
			}
		};
	}
}
function createEditorModalBereitschaftsZeit(row: CustomTable | Row, title: string): CustomHTMLDivElement {
	const { modal, form } = createModal(
		title,
		true,
		"sm",
		createBodyElement,
		createEditorModalFooter(null, row instanceof Row ? "Speichern" : null),
		SubmitEventListener
	);

	return modal;

	function createBodyElement(): HTMLDivElement {
		const modalBody = document.createElement("div");
		modalBody.className = "modal-body row";

		if (row instanceof Row) {
			row.columns.array.forEach(column => {
				let datum, min, max;
				switch (column.name) {
					case "editing":
						break;
					case "beginB":
					case "endeB":
						datum = dayjs(row.cells[column.name]);
						min = datum.startOf("M").format("YYYY-MM-DDTHH:mm");
						max = datum.add(1, "M").startOf("M").format("YYYY-MM-DDTHH:mm");
						modalBody.appendChild(
							createModalBodyInputElement({
								divClass: "form-floating col-12 pb-3",
								title: column.title,
								name: column.name,
								value: dayjs(row.cells[column.name]).format("YYYY-MM-DDTHH:mm"),
								type: "datetime-local",
								required: true,
								min,
								max,
							})
						);
						break;
					default:
						modalBody.appendChild(
							createModalBodyInputElement({
								divClass: "form-floating col-12",
								title: column.title,
								name: column.name,
								value: column.parser(row.cells[column.name]),
								type: "number",
								min: 0,
								max: 60,
							})
						);
						break;
				}
			});
		} else if (row instanceof CustomTable) {
			const Monat = Storage.get<number>("Monat");
			const Jahr = Storage.get<number>("Jahr");
			row.columns.array.forEach(column => {
				let datum, min, max;
				switch (column.name) {
					case "editing":
						break;
					case "beginB":
					case "endeB":
						datum = dayjs([Jahr, Monat - 1, checkMaxTag(Jahr, Monat - 1)]);
						min = datum.startOf("M").format("YYYY-MM-DDTHH:mm");
						max = datum.add(1, "M").startOf("M").format("YYYY-MM-DDTHH:mm");
						modalBody.appendChild(
							createModalBodyInputElement({
								divClass: "form-floating col-12 pb-3",
								title: column.title,
								name: column.name,
								value: null,
								type: "datetime-local",
								required: true,
								min,
								max,
							})
						);
						break;
					default:
						modalBody.appendChild(
							createModalBodyInputElement({
								divClass: "form-floating col-12",
								title: column.title,
								name: column.name,
								value: "",
								type: "number",
								min: 0,
								max: 60,
							})
						);
						break;
				}
			});
		}

		return modalBody;
	}

	function SubmitEventListener(): (event: Event) => void {
		return (event: Event) => {
			if (form instanceof HTMLDivElement) return;
			if (form.checkValidity && !form.checkValidity()) return;
			event.preventDefault();

			const row = modal.row;
			if (!row) throw new Error("Row nicht gefunden");
			const table = row instanceof Row ? row.CustomTable : row;

			const formData = toJSON<IDaten["BZ"][0]>(new FormData(form), table);
			const values: IDaten["BZ"][0] = {
				beginB: dayjs(formData.beginB).toISOString(),
				endeB: dayjs(formData.endeB).toISOString(),
				pauseB: Number(formData.pauseB),
			};

			row instanceof Row ? row.val(values) : row.rows.add(values);

			(<Modal>Modal.getInstance(modal)).hide();
			saveTableData("tableBZ", table);
		};
	}
}

function createAddModalBereitschaftsEinsatz(): void {
	const { modal, form } = createModal(
		"Neuen Bereitschaftseinsatz eingeben",
		true,
		null,
		createBodyElement,
		createEditorModalFooter(),
		SubmitEventListener
	);
	new Modal(modal).show();

	function createBodyElement(): HTMLDivElement {
		const modalBody = document.createElement("div");
		modalBody.className = "modal-body row";

		const Jahr = Storage.get<number>("Jahr");
		const Monat = Storage.get<number>("Monat") - 1;
		const datum = dayjs([Jahr, Monat, checkMaxTag(Jahr, Monat)]);

		modalBody.appendChild(
			createModalBodyInputElement({
				divClass: "form-floating col-12 col-sm-6 pb-3",
				title: "Datum",
				name: "Datum",
				value: datum.format("YYYY-MM-DD"),
				type: "date",
				required: true,
				min: datum.startOf("M").format("YYYY-MM-DD"),
				max: datum.endOf("M").format("YYYY-MM-DD"),
			})
		);
		modalBody.appendChild(
			createModalBodyInputElement({
				divClass: "form-floating col-12 pb-3",
				title: "SAP-Nr / Einsatzbeschreibung",
				name: "SAPNR",
				value: "",
				type: "text",
				required: true,
			})
		);
		modalBody.appendChild(
			createModalBodyInputElement({
				divClass: "form-floating col-12 col-sm-6 pb-3",
				title: "Von",
				name: "ZeitVon",
				value: null,
				type: "time",
				required: true,
			})
		);
		modalBody.appendChild(
			createModalBodyInputElement({
				divClass: "form-floating col-12 col-sm-6 pb-3",
				title: "Bis",
				name: "ZeitBis",
				value: null,
				type: "time",
				required: true,
			})
		);
		modalBody.appendChild(
			createModalBodySelectElement({
				divClass: "form-floating col-12 col-sm-6 pb-3",
				title: "LRE",
				name: "LRE",
				value: null,
				options: [
					{ value: "", text: "Bitte Einsatz auswhlen", disabled: true, selected: true },
					{ value: "LRE 1", text: "LRE 1" },
					{ value: "LRE 2", text: "LRE 2" },
					{ value: "LRE 1/2 ohne x", text: "LRE 1/2 ohne x" },
					{ value: "LRE 3", text: "LRE 3" },
					{ value: "LRE 3 ohne x", text: "LRE 3 ohne x" },
				],
				required: true,
			})
		);
		modalBody.appendChild(createModalBodyFillerElement());
		modalBody.appendChild(
			createModalBodyInputElement({
				divClass: "form-floating col-12 col-sm-6 pb-3",
				title: "Privat Km",
				name: "privatkm",
				value: "",
				type: "number",
				min: 0,
			})
		);

		const wrapper = document.createElement("div");
		wrapper.className = "col-12";

		wrapper.appendChild(
			createModalBodyCheckbox({
				checkClass: "form-check form-switch bereitschaft",
				id: "berZeit",
				text: "zustzliche Bereitschaftszeit Eingeben?<br><small>(z.B. LRE3 Auerhalb der Bereitschaft.)</small>",
				status: false,
			})
		);
		modalBody.appendChild(wrapper);

		return modalBody;
	}

	function SubmitEventListener(): (event: Event) => void {
		return (event: Event) => {
			if (form instanceof HTMLFormElement && !form.checkValidity()) return;
			event.preventDefault();

			sendDataE(modal);

			const table = document.querySelector<CustomHTMLTableElement>("#tableBE");

			(<Modal>Modal.getInstance(modal)).hide();

			if (table) {
				saveTableData("tableBE", table.instance);
			}
		};
	}
}
function createEditorModalBereitschaftsEinsatz(row: CustomTable | Row, titel: string): CustomHTMLDivElement {
	const { modal, form } = createModal(
		titel,
		true,
		null,
		createBodyElement,
		createEditorModalFooter(null, row instanceof Row ? "Speichern" : null),
		SubmitEventListener
	);

	return modal;

	function createBodyElement(): HTMLDivElement {
		const modalBody = document.createElement("div");
		modalBody.className = "modal-body row";

		let datum: dayjs.Dayjs;
		if (row instanceof Row) {
			datum = dayjs(row.cells.tagBE, "DD.MM.YYYY");
		} else if (row instanceof CustomTable) {
			const Monat = Storage.get<number>("Monat");
			const Jahr = Storage.get<number>("Jahr");
			datum = dayjs([Jahr, Monat - 1, checkMaxTag(Jahr, Monat - 1)]);
		} else throw new Error("unbekannter Fehler");

		row.columns.array.forEach(column => {
			switch (column.name) {
				case "tagBE":
					modalBody.appendChild(
						createModalBodyInputElement({
							divClass: "form-floating col-12 col-sm-6 pb-3",
							title: column.title,
							name: column.name,
							value: datum.format("YYYY-MM-DD"),
							type: "date",
							required: true,
							min: datum.startOf("M").format("YYYY-MM-DD"),
							max: datum.endOf("M").format("YYYY-MM-DD"),
						})
					);
					break;

				case "auftragsnummerBE":
					modalBody.appendChild(
						createModalBodyInputElement({
							divClass: "form-floating col-12 pb-3",
							title: "SAP-Nr / Einsatzbeschreibung",
							name: column.name,
							value: row instanceof Row ? row.cells[column.name] : "",
							type: "text",
							required: true,
						})
					);
					break;

				case "beginBE":
				case "endeBE":
					modalBody.appendChild(
						createModalBodyInputElement({
							divClass: "form-floating col-12 col-sm-6 pb-3",
							title: column.title,
							name: column.name,
							value: row instanceof Row ? row.cells[column.name] : "",
							type: "time",
							required: true,
						})
					);
					break;

				case "lreBE":
					modalBody.appendChild(
						createModalBodySelectElement({
							divClass: "form-floating col-12 col-sm-6 pb-3",
							title: column.title,
							name: column.name,
							value: row instanceof Row ? row.cells[column.name] : "",
							options: [
								{ value: "", text: "Bitte Einsatz auswhlen", disabled: true, selected: true },
								{ value: "LRE 1", text: "LRE 1" },
								{ value: "LRE 2", text: "LRE 2" },
								{ value: "LRE 1/2 ohne x", text: "LRE 1/2 ohne x" },
								{ value: "LRE 3", text: "LRE 3" },
								{ value: "LRE 3 ohne x", text: "LRE 3 ohne x" },
							],
							required: true,
						})
					);
					modalBody.appendChild(createModalBodyFillerElement());
					break;

				case "privatkmBE":
					modalBody.appendChild(
						createModalBodyInputElement({
							divClass: "form-floating col-12 col-sm-6",
							title: column.title,
							name: column.name,
							value: row instanceof Row ? row.cells[column.name] : "",
							type: "number",
							min: 0,
						})
					);
					break;

				default:
					break;
			}
		});

		return modalBody;
	}

	function SubmitEventListener(): (event: Event) => void {
		return (event: Event) => {
			if (!(form instanceof HTMLFormElement)) return;
			event.preventDefault();
			if (!form.checkValidity()) return;

			const row = modal.row;
			if (!row) throw new Error("Row nicht gefunden");
			const table = row instanceof Row ? row.CustomTable : row;

			const values: IDaten["BE"][0] = {
				tagBE: dayjs(form.querySelector<HTMLInputElement>("#tagBE")?.value).format("DD.MM.)YYYY") ?? "",
				auftragsnummerBE: form.querySelector<HTMLInputElement>("#auftragsnummerBE")?.value ?? "",
				beginBE: form.querySelector<HTMLInputElement>("#beginBE")?.value ?? "",
				endeBE: form.querySelector<HTMLInputElement>("#endeBE")?.value ?? "",
				lreBE: form.querySelector<HTMLSelectElement>("#lreBE")?.value ?? "",
				privatkmBE: Number(form.querySelector<HTMLInputElement>("#privatkmBE")?.value) ?? 0,
			};

			row instanceof Row ? row.val(values) : row.rows.add(values);

			(<Modal>Modal.getInstance(modal)).hide();
			saveTableData("tableBE", table);
		};
	}
}

function createShowModalBereitschaft(row: Row, title: string): CustomHTMLDivElement {
	const { modal } = createModal(title, false, "sm", createModalBody, createShowModalFooter);
	return modal;

	function createModalBody() {
		const modalBody = document.createElement("div");
		modalBody.className = "modal-body";

		row.columns.array.forEach(column => {
			if (column.editing) return;
			modalBody.appendChild(
				createModalBodyShowElement({
					divClass: "",
					labelClass: "col-5 col-form-label text-wrap fw-bold",
					spanClass: "col-7 align-middle text-break my-auto",
					title: `${column.title}:`,
					name: column.name,
					text: column.parser(row.cells[column.name]),
				})
			);
		});

		return modalBody;
	}
}

// convert Daten

export function DataBZ(data?: IDaten["BZ"]): IDaten["BZ"] {
	if (data === undefined) {
		if (!Storage.check("dataBZ")) return [];
		data = Storage.get("dataBZ");
	}
	if (data === undefined) return [];
	return data;
}

export function DataBE(data?: IDaten["BE"]): IDaten["BE"] {
	if (data === undefined) {
		if (!Storage.check("dataBE")) return [];
		data = Storage.get("dataBE");
	}
	if (data === undefined) return [];
	return data;
}

// Eingabemaske Bereitschaftszeiten

function bereitschaftsVorgabeAEndern(
	parentElement: HTMLDivElement,
	vorgabenB: IVorgabenU["vorgabenB"][0],
	datum = dayjs(parentElement.querySelector<HTMLInputElement>("#bA")?.value) ?? null
): void {
	if (!datum) throw new Error("Datum nicht gefunden");

	const bAInput = parentElement.querySelector<HTMLInputElement>("#bA");
	const bATInput = parentElement.querySelector<HTMLInputElement>("#bAT");
	const bEInput = parentElement.querySelector<HTMLInputElement>("#bE");
	const bETInput = parentElement.querySelector<HTMLInputElement>("#bET");
	const nachtInput = parentElement.querySelector<HTMLInputElement>("#nacht");
	const nAInput = parentElement.querySelector<HTMLInputElement>("#nA");
	const nATInput = parentElement.querySelector<HTMLInputElement>("#nAT");
	const nEInput = parentElement.querySelector<HTMLInputElement>("#nE");
	const nETInput = parentElement.querySelector<HTMLInputElement>("#nET");

	if (!bAInput || !bATInput || !bEInput || !bETInput || !nachtInput || !nAInput || !nATInput || !nEInput || !nETInput) {
		throw new Error("Input Element nicht gefunden");
	}

	bAInput.value = datum.isoWeekday(vorgabenB.beginnB.tag).format("YYYY-MM-DD");
	bATInput.value = vorgabenB.beginnB.zeit;
	bEInput.value = datum
		.isoWeekday(vorgabenB.endeB.tag)
		.add(vorgabenB.endeB.Nwoche ? 7 : 0, "d")
		.format("YYYY-MM-DD");
	bETInput.value = vorgabenB.endeB.zeit;
	nachtInput.checked = vorgabenB.nacht;
	nAInput.value = datum
		.isoWeekday(vorgabenB.beginnN.tag)
		.add(vorgabenB.beginnN.Nwoche ? 7 : 0, "d")
		.format("YYYY-MM-DD");
	nATInput.value = vorgabenB.beginnN.zeit;
	nEInput.value = datum
		.isoWeekday(vorgabenB.endeN.tag)
		.add(vorgabenB.endeN.Nwoche ? 7 : 0, "d")
		.format("YYYY-MM-DD");
	nETInput.value = vorgabenB.endeN.zeit;

	nachtAusblenden(parentElement);
}

function nachtAusblenden(parentElement: HTMLDivElement): void {
	const nachtCheckbox = parentElement.querySelector<HTMLInputElement>("#nacht");
	const nachtschichtDiv = parentElement.querySelector<HTMLDivElement>("#nachtschicht");
	if (nachtCheckbox && nachtschichtDiv) {
		nachtschichtDiv.style.display = nachtCheckbox.checked ? "flex" : "none";
	}
}

function datumAnpassen(parentElement: HTMLDivElement, vorgabenB: IVorgabenU["vorgabenB"][0], datum: dayjs.Dayjs): void {
	const bE = parentElement.querySelector<HTMLInputElement>("#bE");
	const bET = parentElement.querySelector<HTMLInputElement>("#bET");
	const nA = parentElement.querySelector<HTMLInputElement>("#nA");
	const nAT = parentElement.querySelector<HTMLInputElement>("#nAT");
	const nE = parentElement.querySelector<HTMLInputElement>("#nE");
	const nET = parentElement.querySelector<HTMLInputElement>("#nET");

	if (!bE || !bET || !nA || !nAT || !nE || !nET) {
		throw new Error("Element not found");
	}

	bE.value = datum
		.isoWeekday(vorgabenB.endeB.tag)
		.add(vorgabenB.endeB.Nwoche ? 7 : 0, "d")
		.format("YYYY-MM-DD");
	bET.value = vorgabenB.endeB.zeit;
	nA.value = datum
		.isoWeekday(vorgabenB.beginnN.tag)
		.add(vorgabenB.beginnN.Nwoche ? 7 : 0, "d")
		.format("YYYY-MM-DD");
	nAT.value = vorgabenB.beginnN.zeit;
	nE.value = datum
		.isoWeekday(vorgabenB.endeN.tag)
		.add(vorgabenB.endeN.Nwoche ? 7 : 0, "d")
		.format("YYYY-MM-DD");
	nET.value = vorgabenB.endeN.zeit;
}

function eigeneWerte(parentElement: HTMLDivElement, vorgabenB: IVorgabenU["vorgabenB"][0], datum: dayjs.Dayjs): void {
	const bATInput = parentElement.querySelector<HTMLInputElement>("#bAT");
	const bEInput = parentElement.querySelector<HTMLInputElement>("#bE");
	const bETInput = parentElement.querySelector<HTMLInputElement>("#bET");
	const nAInput = parentElement.querySelector<HTMLInputElement>("#nA");
	const nATInput = parentElement.querySelector<HTMLInputElement>("#nAT");
	const nEInput = parentElement.querySelector<HTMLInputElement>("#nE");
	const nETInput = parentElement.querySelector<HTMLInputElement>("#nET");
	const eigenCheckbox = parentElement.querySelector<HTMLInputElement>("#eigen");

	if (!bATInput || !bEInput || !bETInput || !nAInput || !nATInput || !nEInput || !nETInput || !eigenCheckbox)
		throw new Error("Input Element nicht gefunden");

	if (eigenCheckbox.checked) {
		bATInput.disabled = false;
		bEInput.disabled = false;
		bETInput.disabled = false;
		nAInput.disabled = false;
		nATInput.disabled = false;
		nEInput.disabled = false;
		nETInput.disabled = false;
	} else {
		bATInput.disabled = true;
		bEInput.disabled = true;
		bETInput.disabled = true;
		nAInput.disabled = true;
		nATInput.disabled = true;
		nEInput.disabled = true;
		nETInput.disabled = true;
		bereitschaftsVorgabeAEndern(parentElement, vorgabenB, datum);
	}
}

// Eingabemaske Bereitschafteinsatz
function sendDataE($modal: HTMLDivElement): void {
	setLoading("btnESE");

	const datumInput = $modal.querySelector<HTMLInputElement>("#Datum");
	const sapnrInput = $modal.querySelector<HTMLInputElement>("#SAPNR");
	const vonInput = $modal.querySelector<HTMLInputElement>("#ZeitVon");
	const bisInput = $modal.querySelector<HTMLInputElement>("#ZeitBis");
	const lreSelect = $modal.querySelector<HTMLSelectElement>("#LRE");
	const privatkmInput = $modal.querySelector<HTMLInputElement>("#privatkm");
	const berZeitInput = $modal.querySelector<HTMLInputElement>("#berZeit");
	const tableBE = document.querySelector<CustomHTMLTableElement>("#tableBE");
	const tableBZ = document.querySelector<CustomHTMLTableElement>("#tableBZ");

	if (
		!datumInput ||
		!sapnrInput ||
		!vonInput ||
		!bisInput ||
		!lreSelect ||
		!privatkmInput ||
		!berZeitInput ||
		!tableBE ||
		!tableBZ
	) {
		throw new Error("Input Element nicht gefunden");
	}

	const daten = {
		tagBE: dayjs(datumInput.value).format("DD.MM.YYYY"),
		auftragsnummerBE: sapnrInput.value,
		beginBE: vonInput.value,
		endeBE: bisInput.value,
		lreBE: lreSelect.value,
		privatkmBE: Number(privatkmInput.value),
	};

	const berZeit = berZeitInput.checked;

	console.log(daten);
	const ftBE = tableBE.instance;
	ftBE.rows.add(daten);
	saveTableData("tableBE", ftBE);

	sapnrInput.value = "";
	vonInput.value = "";
	bisInput.value = "";
	privatkmInput.value = "";
	berZeitInput.checked = false;
	lreSelect.value = "";

	if (berZeit) {
		daten.tagBE = datumInput.value;
		const bereitschaftsAnfang = dayjs(`${daten.tagBE}T${daten.beginBE}`),
			bereitschaftsEnde = dayjs(`${daten.tagBE}T${daten.endeBE}`).isBefore(bereitschaftsAnfang)
				? dayjs(`${daten.tagBE}T${daten.endeBE}`).add(1, "d")
				: dayjs(`${daten.tagBE}T${daten.endeBE}`),
			dataTable = tableToArray<IDaten["BZ"][0]>("tableBZ");

		const data = BereitschaftEingabe(
			bereitschaftsAnfang,
			bereitschaftsEnde,
			bereitschaftsEnde,
			bereitschaftsEnde,
			false,
			dataTable
		);

		if (!data) {
			clearLoading("btnESE");
			createSnackBar({
				message: "Bereitschaft<br/>Zeitraum bereits vorhanden!",
				status: "warning",
				timeout: 3000,
				position: "br",
				fixed: true,
			});
			return;
		}

		Storage.set("dataBZ", data);
		tableBZ.instance.rows.load(DataBZ(data));
		createSnackBar({
			message:
				"Bereitschaft<br/>Neuer Zeitraum hinzugefgt</br>Speichern nicht vergessen!</br></br>Berechnung wird erst nach Speichern aktualisiert.",
			status: "success",
			timeout: 3000,
			position: "br",
			fixed: true,
		});
	}

	clearLoading("btnESE");
}

// Berechnung Bereitschaft
/**
 *
 * @param {HTMLElement} $modal
 * @param {string} accessToken
 * @returns
 */
async function bereitschaftEingabeWeb($modal: CustomHTMLDivElement, accessToken: string): Promise<void> {
	setLoading("btnESZ");

	const bAInput = $modal.querySelector<HTMLInputElement>("#bA");
	const bATInput = $modal.querySelector<HTMLInputElement>("#bAT");
	const bEInput = $modal.querySelector<HTMLInputElement>("#bE");
	const bETInput = $modal.querySelector<HTMLInputElement>("#bET");
	const nachtInput = $modal.querySelector<HTMLInputElement>("#nacht");
	const nAInput = $modal.querySelector<HTMLInputElement>("#nA");
	const nATInput = $modal.querySelector<HTMLInputElement>("#nAT");
	const nEInput = $modal.querySelector<HTMLInputElement>("#nE");
	const nETInput = $modal.querySelector<HTMLInputElement>("#nET");
	const MonatInput = document.querySelector<HTMLInputElement>("#Monat");
	const JahrInput = document.querySelector<HTMLInputElement>("#Jahr");
	const tableBZ = document.querySelector<CustomHTMLTableElement>("#tableBZ");

	if (
		!bAInput ||
		!bATInput ||
		!bEInput ||
		!bETInput ||
		!nachtInput ||
		!nAInput ||
		!nATInput ||
		!nEInput ||
		!nETInput ||
		!MonatInput ||
		!JahrInput ||
		!tableBZ
	)
		throw new Error("Input Element nicht gefunden");

	const bereitschaftsAnfang = dayjs(`${bAInput.value}T${bATInput.value}`);
	const bereitschaftsEnde = dayjs(`${bEInput.value}T${bETInput.value}`);
	const nacht = nachtInput.checked;
	let nachtAnfang: dayjs.Dayjs;
	let nachtEnde: dayjs.Dayjs;
	if (nacht === true) {
		nachtAnfang = dayjs(`${nAInput.value}T${nATInput.value}`);
		nachtEnde = dayjs(`${nEInput.value}T${nETInput.value}`);
	} else {
		nachtAnfang = bereitschaftsEnde;
		nachtEnde = bereitschaftsEnde;
	}

	const monat = +MonatInput.value;
	const jahr = +JahrInput.value;

	let data: IDaten["BZ"] | false = [];
	let data1: IDaten["BZ"] | false = tableToArray("tableBZ");
	let data2: IDaten["BZ"] | false = [];
	if (!data1) throw new Error("Fehler bei Datenermittlung");
	console.log({ bereitschaftsAnfang, bereitschaftsEnde, nachtAnfang, nachtEnde, nacht, monat, jahr, accessToken });
	if (
		bereitschaftsAnfang.isSame(bereitschaftsEnde, "M") ||
		(!bereitschaftsAnfang.isSame(bereitschaftsEnde, "M") &&
			bereitschaftsEnde.isSameOrBefore(dayjs([jahr, bereitschaftsEnde.month(), 1, 0, 0])))
	) {
		data = BereitschaftEingabe(bereitschaftsAnfang, bereitschaftsEnde, nachtAnfang, nachtEnde, nacht, data1);
	} else if (!navigator.onLine) {
		createSnackBar({
			message: "Bereitschaft<br/>Du bist Offline: <br/>Kein Monatswechsel mglich!",
			icon: "question",
			status: "error",
			dismissible: false,
			timeout: false,
			position: "br",
			actions: [
				{
					text: "ohne wechsel fortsetzten?",
					function: () => {
						if (!data1) throw new Error("Fehler bei Datenermittlung");
						data = BereitschaftEingabe(
							bereitschaftsAnfang,
							dayjs([bereitschaftsEnde.year(), bereitschaftsEnde.month()]),
							nachtAnfang,
							nachtEnde,
							nacht,
							data1
						);
						if (!data) {
							clearLoading("btnESZ");
							createSnackBar({
								message: "Bereitschaft<br/>Bereitschaftszeitraum Bereits vorhanden!",
								icon: "!",
								status: "warning",
								timeout: 3000,
								position: "br",
								fixed: true,
							});
							return;
						}

						Storage.set("dataBZ", data);
						tableBZ.instance.rows.load(DataBZ(data));

						clearLoading("btnESZ");
						createSnackBar({
							message:
								"Bereitschaft<br/>Neuer Zeitraum hinzugefgt</br>Speichern nicht vergessen!</br></br>Berechnung wird erst nach Speichern aktualisiert.",
							status: "success",
							timeout: 3000,
							position: "br",
							fixed: true,
						});
					},
					dismiss: true,
					class: ["text-primary"],
				},
				{
					text: "Abbrechen",
					function: () => {
						clearLoading("btnESZ");
					},
					dismiss: true,
				},
			],
			fixed: true,
		});
		return;
	} else {
		const monat2 = bereitschaftsEnde.month();
		const jahr2 = bereitschaftsEnde.year();

		const bereitschaftsEndeWechsel = dayjs([jahr2, monat2]);
		let nachtEnde1;
		let nachtAnfang2;
		let bereitschaftsEndeWechsel2;
		if (bereitschaftsEndeWechsel.isBefore(nachtAnfang)) {
			nachtEnde1 = nachtEnde.clone();
			nachtAnfang2 = nachtAnfang.clone();
			bereitschaftsEndeWechsel2 = bereitschaftsEndeWechsel.clone();
		} else if (bereitschaftsEndeWechsel.isAfter(nachtEnde)) {
			nachtEnde1 = nachtEnde.clone();
			bereitschaftsEndeWechsel2 = bereitschaftsEndeWechsel.clone();
			nachtAnfang2 = bereitschaftsEnde.clone();
			nachtEnde = bereitschaftsEnde.clone();
		} else if (bereitschaftsEndeWechsel.isAfter(nachtAnfang) && bereitschaftsEndeWechsel.isBefore(nachtEnde)) {
			nachtEnde1 = dayjs([jahr2, monat2, 1, nachtEnde.hour(), nachtEnde.minute()]);
			nachtAnfang2 = dayjs([jahr2, monat2, 1, nachtAnfang.hour(), nachtAnfang.minute()]);
			bereitschaftsEndeWechsel2 = nachtEnde1.clone();
		} else {
			throw new Error("Fehler bei Nacht und Bereitschaft");
		}

		data1 = BereitschaftEingabe(bereitschaftsAnfang, bereitschaftsEndeWechsel, nachtAnfang, nachtEnde1, nacht, data1);

		try {
			const fetched2 = await FetchRetry<null, { datenB: { datenBZ: IDaten["BZ"] } }>(`${monat2 + 1}&${jahr2}`);
			if (fetched2.statusCode != 200) {
				console.log("Fehler");
				return;
			}
			const dataResponded2 = fetched2.data.datenB.datenBZ;
			console.log(dataResponded2);

			data2 = BereitschaftEingabe(
				bereitschaftsEndeWechsel2,
				bereitschaftsEnde,
				nachtAnfang2,
				nachtEnde,
				nacht,
				dataResponded2
			);

			const dataSave = {
				BZ: data2,
				Monat: monat2 + 1,
				Jahr: jahr2,
			};

			const fetchedSave = await FetchRetry("saveData", dataSave, "POST");
			if (fetchedSave.statusCode != 200) {
				console.log("Fehler:", fetchedSave.message);
				createSnackBar({
					message: "Bereitschaft<br/>Es ist ein Fehler beim Monatswechsel aufgetreten",
					status: "error",
					timeout: 3000,
					position: "br",
					fixed: true,
				});
				return;
			}
			console.log(fetchedSave.data);
			createSnackBar({
				message: `Bereitschaft<br/>Daten fr Monat ${monat2 + 1} gespeichert`,
				status: "success",
				timeout: 3000,
				position: "br",
				fixed: true,
			});
		} catch (err) {
			console.log(err);
			return;
		}

		console.log("Daten Monat 1", data1);
		console.log("Daten Monat 2", data2);

		data = data1;
	}
	if (!data) {
		clearLoading("btnESZ");
		createSnackBar({
			message: "Bereitschaft<br/>Bereitschaftszeitraum Bereits vorhanden!",
			status: "warning",
			timeout: 3000,
			position: "br",
			fixed: true,
		});
		return;
	}

	Storage.set("dataBZ", data);
	tableBZ.instance.rows.load(DataBZ(data));

	clearLoading("btnESZ");
	createSnackBar({
		message:
			"Bereitschaft<br/>Neuer Zeitraum hinzugefgt</br>Speichern nicht vergessen!</br></br>Berechnung wird erst nach Speichern aktualisiert.",
		status: "success",
		timeout: 3000,
		position: "br",
		fixed: true,
	});
}

export function BereitschaftEingabe(
	bereitschaftsAnfang: dayjs.Dayjs,
	bereitschaftsEnde: dayjs.Dayjs,
	nachtAnfang: dayjs.Dayjs,
	nachtEnde: dayjs.Dayjs,
	nacht: boolean,
	daten: IDaten["BZ"]
): IDaten["BZ"] | false {
	console.groupCollapsed("Vorgaben");
	console.log("nacht: " + nacht);
	console.log("Bereitschafts Anfang: " + bereitschaftsAnfang.toDate());
	console.log("Bereitschafts Ende: " + bereitschaftsEnde.toDate());
	console.log("Nacht Anfang: " + nachtAnfang.toDate());
	console.log("Nacht Ende: " + nachtEnde.toDate());
	console.groupEnd();

	let bereitschaftsAnfangNacht,
		bereitschaftsAnfangMerker,
		bereitschaftsAnfangA,
		bereitschaftsEndeMerker,
		bereitschaftsEndeA,
		bereitschaftsEndeB,
		arbeitstagHeute,
		arbeitstagMorgen,
		pause,
		pauseMerker,
		pauseMerkerTag,
		pauseMerkerNacht;

	const getTimeForDuration = (value: string) => {
			const time = dayjs(value, "HH:mm");
			return { hours: time.hour(), minutes: time.minute() };
		},
		// Voreinstellungen bernehmen
		datenU: IVorgabenU = Storage.get("VorgabenU"),
		tagAnfangsZeitMoDo = dayjs.duration(getTimeForDuration(datenU.aZ.eT)), // Tagschicht Anfangszeit Mo-Do
		tagAnfangsZeitFr = dayjs.duration(getTimeForDuration(datenU.aZ.eTF)), // Tagschicht Anfangszeit Fr
		tagEndeZeitMoFr = dayjs.duration(getTimeForDuration(datenU.aZ.bT)), // Tagschicht Endezeit Mo-Fr
		// Feste Variablen
		ruheZeit = 10,
		tagPausenVorgabe = 30,
		nachtPausenVorgabe = 45,
		bereitschaftsZeitraumWechsel = dayjs.duration({ hours: 8 }),
		arbeitsAnfang = [
			tagEndeZeitMoFr,
			tagEndeZeitMoFr,
			tagEndeZeitMoFr,
			tagEndeZeitMoFr,
			tagEndeZeitMoFr,
			bereitschaftsZeitraumWechsel,
			bereitschaftsZeitraumWechsel,
			tagEndeZeitMoFr,
		],
		arbeitsEnde = [
			tagAnfangsZeitMoDo,
			tagAnfangsZeitMoDo,
			tagAnfangsZeitMoDo,
			tagAnfangsZeitMoDo,
			tagAnfangsZeitFr,
			bereitschaftsZeitraumWechsel,
			bereitschaftsZeitraumWechsel,
			tagAnfangsZeitMoDo,
		],
		pausenTag = [0, tagPausenVorgabe, tagPausenVorgabe, tagPausenVorgabe, tagPausenVorgabe, 0, 0, 0],
		B2 = (
			bereitschaftsAnfang: dayjs.Dayjs,
			nachtAnfang: dayjs.Dayjs,
			arbeitsAnfang: duration.Duration[],
			arbeitstagHeute: boolean,
			arbeitstagMorgen: boolean,
			nacht: boolean
		): dayjs.Dayjs => {
			console.group("B2");
			let merker: dayjs.Dayjs | undefined;
			const tagBereitschaftsAnfang = bereitschaftsAnfang.day();
			console.log(`Wochentag Bereitschafts Anfang: ${tagBereitschaftsAnfang} --- ${bereitschaftsAnfang.format("dd")}`);
			// neues Ende 2,
			// am nchsten Tag,
			// Begin normale Schicht bzw. neuer Bereitschaftszyklus
			if (!bereitschaftsAnfang.isSame(nachtAnfang, "d")) {
				if (arbeitstagMorgen === true) {
					merker = bereitschaftsAnfang.add(1, "d").startOf("d").add(arbeitsAnfang[tagBereitschaftsAnfang]);
				} else {
					merker = bereitschaftsAnfang.add(1, "d").startOf("d").add(bereitschaftsZeitraumWechsel);
				}

				console.log(`Merker B2.1: ${merker.toDate()}`);
			}
			// Wie oben, aber:
			// 2x Ende am gleichen Tag
			if (bereitschaftsAnfang.isSame(nachtAnfang, "d") || nacht === false) {
				if (arbeitstagHeute === true) {
					merker = bereitschaftsAnfang
						.startOf("d")
						.add(arbeitsAnfang[tagBereitschaftsAnfang === 0 ? 6 : tagBereitschaftsAnfang - 1]);
				} else {
					merker = bereitschaftsAnfang.startOf("d").add(bereitschaftsZeitraumWechsel);
				}

				console.log(`Merker B2.2: ${merker.toDate()}`);
			}
			// Wie oben, aber:
			// wenn merker kleiner/gleich Bereitschafts Anfang -> am nchsten Tag
			if (merker?.isSameOrBefore(bereitschaftsAnfang)) {
				if (arbeitstagMorgen === true) {
					merker = bereitschaftsAnfang.add(1, "d").startOf("d").add(arbeitsAnfang[tagBereitschaftsAnfang]);
				} else {
					merker = bereitschaftsAnfang.add(1, "d").startOf("d").add(bereitschaftsZeitraumWechsel);
				}

				console.log(`Merker B2.3: ${merker.toDate()}`);
			}
			console.groupEnd();
			return merker as dayjs.Dayjs;
		},
		Arbeitstag = (datum: dayjs.Dayjs, zusatz = 0) => {
			datum = datum.add(zusatz, "d");
			const feiertag = isHoliday(datum.toDate(), "HE");
			return !(feiertag || datum.isoWeekday() > 5);
		};

	// Berechne ob Tag ein Arbeitstag ist
	arbeitstagHeute = Arbeitstag(bereitschaftsAnfang);
	console.log(`Arbeitstag Heute: ${arbeitstagHeute} --- ${bereitschaftsAnfang.format("dd")}`);
	arbeitstagMorgen = Arbeitstag(bereitschaftsAnfang, 1);
	console.log(`Arbeitstag Morgen: ${arbeitstagMorgen} --- ${bereitschaftsAnfang.add(1, "d").format("dd")}`);

	// Sonstige Variablen Vorbereiten
	bereitschaftsEndeMerker = bereitschaftsAnfang.clone();

	const datenVorher = daten.length;

	/// --- Beginn Berechnung --- ///
	while (daten.length < 26 && bereitschaftsAnfang.isBefore(bereitschaftsEnde)) {
		/// #Berechnung Bereitschaftsende# ///
		console.groupCollapsed("Bereitschafts Ende");
		// neues Ende Arbeitszeit
		bereitschaftsEndeA = B2(bereitschaftsAnfang, nachtAnfang, arbeitsAnfang, arbeitstagHeute, arbeitstagMorgen, nacht);
		console.log(`Bereitschafts Ende A: ${bereitschaftsEndeA.toDate()}`);

		// neues Ende Bereitschaftszyklus
		bereitschaftsEndeB = bereitschaftsAnfang.add(1, "d").hour(8).minute(0);
		console.log(`Bereitschafts Ende B: ${bereitschaftsEndeB.toDate()}`);
		console.groupEnd();

		// berprfe welches Bereitschaftsende Zutrifft
		bereitschaftsEndeMerker = dayjs.min(bereitschaftsEndeA, bereitschaftsEndeB, nachtAnfang, bereitschaftsEnde);
		console.log(`Bereitschafts Ende Merker: ${bereitschaftsEndeMerker.toDate()}`);

		/// #Berechnung Bereitschaftsanfang# ///
		console.groupCollapsed("Bereitschafts Anfang");

		// neuer Bereitschaftsanfang Nacht
		bereitschaftsAnfangNacht = nachtAnfang.add(1, "d").hour(nachtEnde.hour()).minute(nachtEnde.minute());
		console.log(`Bereitschafts Anfang Nacht: ${bereitschaftsAnfangNacht.toDate()}`);

		// neues Ende 2, Arbeitszeit
		bereitschaftsAnfangA = B2(bereitschaftsAnfang, nachtAnfang, arbeitsEnde, arbeitstagHeute, arbeitstagMorgen, nacht);
		console.log(`Bereitschafts Anfang A: ${bereitschaftsAnfangA.toDate()}`);
		console.groupEnd();

		bereitschaftsAnfangMerker = bereitschaftsAnfang.isSame(bereitschaftsEndeMerker, "d")
			? dayjs.min(bereitschaftsAnfangNacht, bereitschaftsAnfangA, bereitschaftsEndeB)
			: dayjs.min(dayjs.max(bereitschaftsAnfangA, bereitschaftsEndeB), bereitschaftsAnfangNacht);

		console.log(`Bereitschafts Anfang Merker: ${bereitschaftsAnfangMerker.toDate()}`);

		/// #Berechnung Pause# ///
		console.groupCollapsed("Berechnung Pause");

		// Pause Tagschicht, Falls keine Pause oder nach Nachtschicht, dann ""
		pauseMerker = bereitschaftsAnfang.startOf("d").add(tagAnfangsZeitMoDo);
		if (pause === nachtPausenVorgabe) {
			pause = 0;
		} else {
			pause = bereitschaftsAnfang.isSame(pauseMerker) ? pausenTag[bereitschaftsAnfang.day()] : 0;
		}

		// Pause Nachtschicht, normal und bei Ruhe nach Nacht
		pauseMerkerNacht = bereitschaftsAnfang.hour(nachtEnde.hour()).minute(nachtEnde.minute());
		pauseMerkerTag = pauseMerker.hour(nachtEnde.hour()).add(ruheZeit, "h").minute(nachtEnde.minute());
		if (bereitschaftsAnfang.isSame(pauseMerkerNacht) || bereitschaftsAnfang.isSame(pauseMerkerTag)) {
			pause = nachtPausenVorgabe;
		}
		console.groupEnd();
		console.log(`Pausen Zeit: ${pause}`);

		console.log(
			`Eingabe Tabelle: ${bereitschaftsAnfang.format("L, LT")} --- ${bereitschaftsEndeMerker.format("L, LT")} --- ${pause}`
		);

		/// #Prfen ob Daten bereits vorhanden# ///

		const newDaten: IDaten["BZ"][0] = {
			beginB: bereitschaftsAnfang.toISOString(),
			endeB: bereitschaftsEndeMerker.toISOString(),
			pauseB: pause,
		};

		if (!daten.some(row => JSON.stringify(row) === JSON.stringify(newDaten))) {
			daten.push(newDaten);
		} else {
			console.log("Bereitschaftszeitraum bereits vorhanden");
		}
		console.group("bernehme Daten neuer Tag");

		/// #bernehme Daten fr nchsten Tag# ///
		// neuen Bereitschaftsanfang bernehmen
		bereitschaftsAnfang = bereitschaftsAnfangMerker.clone();
		console.log("Bereitschafts Anfang: " + bereitschaftsAnfang.toDate());

		// neuen Nachtschichtanfang setzten, Wenn kleiner als Bereitschaftsanfang
		if (bereitschaftsAnfang.isAfter(nachtAnfang)) {
			nachtAnfang = nachtAnfang.add(1, "d");
		}
		console.log(`Nacht Anfang: ${nachtAnfang.toDate()}`);
		if (nachtAnfang.isAfter(nachtEnde) && !nachtAnfang.isSame(bereitschaftsEnde)) {
			nachtAnfang = bereitschaftsEnde.clone();
			nacht = false;
		}
		console.log(`Nacht: ${nacht}`);

		// Berechne ob Tag ein Arbeitstag ist
		arbeitstagHeute = Arbeitstag(bereitschaftsAnfang);
		console.log(`Arbeitstag Heute: ${arbeitstagHeute} --- ${bereitschaftsAnfang.format("dd")}`);
		arbeitstagMorgen = Arbeitstag(bereitschaftsAnfang, 1);
		console.log(`Arbeitstag Morgen: ${arbeitstagMorgen} --- ${bereitschaftsAnfang.add(1, "d").format("dd")}`);
		console.groupEnd();

		if (!nacht) continue;
		if (!bereitschaftsAnfang.isSame(nachtAnfang, "d")) continue;
		if (bereitschaftsAnfang.isSame(nachtAnfang, "d") && bereitschaftsAnfang.isSame(bereitschaftsAnfangA)) continue;
		/// #Berechnung ob Ruhe nach Nacht am Sonntag & Feiertage# ///
		console.group("Prfen ob Ruhe nach Nacht:");

		// neues Ende Arbeitszeit
		bereitschaftsEndeA = B2(bereitschaftsAnfang, nachtAnfang, arbeitsAnfang, arbeitstagHeute, arbeitstagMorgen, nacht);
		console.log(`Bereitschafts Ende A: ${bereitschaftsEndeA.toDate()}`);

		// neues Ende Bereitschaftszyklus
		bereitschaftsEndeB = bereitschaftsAnfang.add(1, "d").startOf("d").add(bereitschaftsZeitraumWechsel);
		console.log(`Bereitschafts Ende B: ${bereitschaftsEndeB.toDate()}`);

		// berprfe welches Bereitschaftsende Zutrifft
		bereitschaftsEndeMerker = dayjs.min(bereitschaftsEndeA, bereitschaftsEndeB, nachtAnfang, bereitschaftsEnde);
		console.log(`Bereitschafts Ende Merker: ${bereitschaftsEndeMerker.toDate()}`);

		// berprfe ob Ruhe nach Nacht ntig ist
		if (
			bereitschaftsAnfang.isSame(nachtAnfang, "d") &&
			bereitschaftsAnfangNacht.isBefore(nachtAnfang.subtract(10, "h")) &&
			bereitschaftsEndeMerker.isAfter(bereitschaftsAnfangNacht.add(1, "h"))
		) {
			bereitschaftsAnfangNacht = bereitschaftsAnfangNacht.add(ruheZeit, "h");
			console.log(`Bereitschafts Anfang Nacht: ${bereitschaftsAnfangNacht.toDate()}`);
			bereitschaftsAnfang = dayjs.max(dayjs.min(bereitschaftsAnfangA, bereitschaftsEndeB), bereitschaftsAnfangNacht);
			console.log(`Bereitschafts Anfang: ${bereitschaftsAnfang.toDate()}`);
			arbeitstagHeute = Arbeitstag(bereitschaftsAnfang);
			console.log(`Arbeitstag Heute: ${arbeitstagHeute} --- ${bereitschaftsAnfang.format("dd")}`);
			arbeitstagMorgen = Arbeitstag(bereitschaftsAnfang, 1);
			console.log(`Arbeitstag Morgen: ${arbeitstagMorgen} --- ${bereitschaftsAnfang.add(1, "d").format("dd")}`);
		}
		console.groupEnd();
	}
	DatenSortieren(daten);

	if (datenVorher == daten.length) {
		console.log("Keine nderung, Bereitschaft bereits vorhanden");
		return false;
	}

	return daten;
}
